<template>
    <div class="homepage">
        <header-desktop-block :content="{title: 'convention bureau', image: 'http://www.placehold.it/1920x525' }" style="height: 525px"></header-desktop-block>

        <div class="container-fluid">
            <div class="row">

                <div class="col-sm-2 col-sm-offset-1">
                    <div class="text-block__filler"></div>
                </div>

                <div class="col-sm-2">
                    <sub-title-block-bureau :subTitle="dummyWhyCascais.subtitle"></sub-title-block-bureau>
                </div>

                <div class="col-sm-6">
                    <body-block :content="dummyWhyCascais" ></body-block>
                    <dynamic-button :copy="dummyWhyCascais.buttonText" :href="dummyWhyCascais.buttonLink"></dynamic-button>
                </div>

            </div>

            <div class="row homepage__upcoming-events">
                <div class="col-sm-2 col-sm-offset-1">
                    <div class="aside">
                        <sub-title-block :subTitle="'Upcoming Events'"></sub-title-block>
                        <dynamic-button :copy="'See All'" :href="dummyWhyCascais.buttonLink"></dynamic-button>
                    </div>
                </div>

                <div class="col-sm-9">
                    <article-card class="col-xs-12 col-sm-6 col-md-3" type="horizontal" :content="events[0]" :showDate="true"></article-card>
                </div>
            </div>
        </div>

        <infinite-loader v-if="!isFirstTime" :show="show" :setScroll="setInfiniteScroll"></infinite-loader>
    </div>
</template>

<script>
    // import of libs
    import ScrollMagic from 'scrollmagic';
    import { mapGetters } from 'vuex';
    import axios from 'axios';
    import { CONSTANTS } from '../main';
    import UpdateLanguage from '../mixins/UpdateLanguage';
    import { createCookie } from '../helpers/Helpers';

    // import of components
    import HeaderDesktopBlock from '../components/HeaderDesktopBlock';
    import BodyBlock from '../components/BodyBlock';
    import SubTitleBlockBureau from '../components/SubTitleBlockBureau';
    import SubTitleBlock from '../components/SubTitleBlock';
    import DynamicButton from '../components/DynamicButton';
    import ArticleCard from '../components/ArticleCard';
    import BackCounter from '../mixins/BackCounter';

    export default {

        mixins: [UpdateLanguage, BackCounter],

        components: {
            HeaderDesktopBlock,
            BodyBlock,
            SubTitleBlockBureau,
            SubTitleBlock,
            DynamicButton,
            ArticleCard,
        },

        computed: {
            ...mapGetters([
                'getActiveLang',
            ]),
        },

        data() {
            return {
                dummyWhyCascais: {
                    bodyText: '<p>But before you unpack, just check to make sure whether the Island has been affected. We have had many tropical storm warnings over the years, but unlike Florida we’ve been extremely fortunate. In our opinion, the best offense is defense. We recommend purchasing travel insurance. Even the most basic travel insurance will provide some compensation if flights are canceled or the island is closed down due to a hurricane.</p>',
                    subtitle: 'Why Cascais',
                    buttonText: 'Come on in',
                    buttonLink: 'https://www.ubuntu.com/',
                },
            };
        },

        created() {
        },

        methods: {
        },
    };
</script>



