<template>
    <section class="related-articles-block">
        <div class="row">
            <div class="col-xs-12 related-articles-block__title">
                <h3>Related Articles</h3>
            </div>
        </div>
        <div class="row">
            <article-card v-for="(article, index) in content.relatedContents" :key="index" class="col-xs-6 col-md-4" type="horizontal" :smaller="true" :content="article"></article-card>
        </div>
    </section>
</template>

<script>

    import ArticleCard from '../components/ArticleCard';

    export default {

        name: 'related-articles-block',

        components: {
            ArticleCard,
        },

        props: {
            content: {
                required: true,
                type: Object,
            },
        },

    };

</script>



