<template>
    <div class="details-event" v-if="eventsInfo !== 'undefined'">
        <div v-if="showDesktop()">
            <header-desktop-block :content="{title: eventsInfo.title, image: eventsInfo.coverImageDetailUrl }" style="height: 350px"></header-desktop-block>
            <div class="container-fluid">
                <div class="row">
                    <div class="col-md-1">
                        <back-icon v-on:back="back" :backButtonText="eventsInfo.sharedDetailInformation.backButtonText"></back-icon>
                    </div>
                    <div class="col-md-10">
                        <div class="row details-event__body details-event__block">
                            <div class="col-md-2" style="position: relative;">
                                <sub-title-block :subTitle="eventsInfo.dateDescription"></sub-title-block>
                                <information-block
                                    :content="eventsInfo.properties"
                                    :socialLinks="eventsInfo.socialLinks"
                                    :address="eventsInfo.address"></information-block>
                            </div>
                            <div class="col-md-6">
                                <body-block :content="eventsInfo" class="details-event-desktop__body-text"></body-block>
                            </div>
                            <div class="col-md-4 details-event__body-right-col">
                                <map-block :mapPoints="eventsInfo.mapPoints" class="details-event-desktop__map"></map-block>
                            </div>
                        </div>
                        <div class="row details-event__block">
                            <div class="col-md-2"></div>
                            <div class="col-md-10">
                                <social-media-block :content="eventsInfo.sharedDetailInformation" :seo="eventsInfo.seo" class="details-event-desktop__social-media" :isInline="true"></social-media-block>
                            </div>
                        </div>
                        <!-- Begin Blocks-->
                        <div class="row details-event__block" v-for="block in eventsInfo.blocks">
                            <embed-block v-if="block.blockDocType === BlockTypes.EMBED" :url="block.embedContent"></embed-block>
                            <text-block v-if="block.blockDocType === BlockTypes.TEXT" :text="block.text" :fullSize="block.fillFullSpace" :title="block.blockTitle"></text-block>
                            <related-articles-block v-if="block.blockDocType === BlockTypes.RELATEDCONTENTS" :content="block"></related-articles-block>
                            <products-block v-if="block.blockDocType === BlockTypes.RELATEDPRODUCTS" :content="block" :addAllText="eventsInfo.sharedDetailInformation.addAllToFavoritesLinkText"></products-block>
                            <files-block v-if="block.blockDocType === BlockTypes.RELATEDFILES" :content="block"></files-block>
                            <resource-block v-if="block.blockDocType === BlockTypes.RESOURCES" :content="block.resource" :seeLocation="articlesInfo.sharedDetailInformation.seeLocationText"></resource-block>
                            <properties-block v-if="block.blockDocType === BlockTypes.PROPERTIES" :content="block" :fullSize="block.fillFullSpace"></properties-block>
                            <call-to-action-block v-if="block.blockDocType === BlockTypes.CALLTOACTION" :content="block" ></call-to-action-block>
                            <gallery-block v-if="block.blockDocType === BlockTypes.GALLERY" :images="block.images"></gallery-block>
                        </div>
                        <!-- End Blocks-->
                    </div>
                    <div class="col-md-1"></div>
                </div>
            </div>
        </div>
        <div v-if="showMobile()">
            <header-mobile-block
                    :content="{title: eventsInfo.title, image: eventsInfo.mobileCoverImageDetailUrl, subtitle: eventsInfo.subtitle, }"
                    :hideFav="true"></header-mobile-block>
            <div class="container-fluid">
                <div class="row">
                    <div class="col-xs-12">
                        <div class="row details-article__block">
                            <div class="col-xs-12">
                                <body-block :content="eventsInfo"></body-block>
                            </div>
                        </div>
                        <div class="row details-article__block">
                            <div class="col-xs-12">
                                <div class="details-event-mobile__social-media">
                                    <social-media-block :content="eventsInfo.sharedDetailInformation" :seo="eventsInfo.seo" :isInline="true"></social-media-block>
                                </div>
                            </div>
                        </div>
                        <!-- Begin Blocks-->
                        <div class="row details-event__block" v-for="block in eventsInfo.blocks">
                            <embed-block v-if="block.blockDocType === BlockTypes.EMBED" :url="block.embedContent"></embed-block>
                            <text-block v-if="block.blockDocType === BlockTypes.TEXT" :text="block.text" :fullSize="true" :title="block.blockTitle"></text-block>
                            <related-articles-block v-if="block.blockDocType === BlockTypes.RELATEDCONTENTS" :content="block"></related-articles-block>
                            <products-block v-if="block.blockDocType === BlockTypes.RELATEDPRODUCTS" :content="block" :addAllText="eventsInfo.sharedDetailInformation.addAllToFavoritesLinkText"></products-block>
                            <files-block v-if="block.blockDocType === BlockTypes.RELATEDFILES" :content="block"></files-block>
                            <resource-block v-if="block.blockDocType === BlockTypes.RESOURCES" :content="block.resource" :seeLocation="eventsInfo.sharedDetailInformation.seeLocationText"></resource-block>
                            <properties-block v-if="block.blockDocType === BlockTypes.PROPERTIES" :content="block" :fullSize="true"></properties-block>
                            <call-to-action-block v-if="block.blockDocType === BlockTypes.CALLTOACTION" :content="block" ></call-to-action-block>
                            <gallery-block v-if="block.blockDocType === BlockTypes.GALLERY" :images="block.images"></gallery-block>
                        </div>
                        <!-- End Blocks-->
                        <div class="row details-event__block">
                            <div class="col-xs-12">
                                <information-block :content="eventsInfo.properties" :address="eventsInfo.address"></information-block>
                            </div>
                        </div>
                        <div class="row">
                            <div class="col-xs-12">
                                <map-block :mapPoints="eventsInfo.mapPoints" style="height: 350px;"></map-block>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import { mapGetters } from 'vuex';
    import axios from 'axios';
    import { CONSTANTS, ENUMS } from '../main';
    import UpdateLanguage from '../mixins/UpdateLanguage';

    import HeaderDesktopBlock from '../components/HeaderDesktopBlock';
    import HeaderMobileBlock from '../components/HeaderMobileBlock';
    import SubTitleBlock from '../components/SubTitleBlock';
    import FavBlock from '../components/FavBlock';
    import MapBlock from '../components/MapBlock';
    import BodyBlock from '../components/BodyBlock';
    import EmbedBlock from '../components/EmbedBlock';
    import SocialMediaBlock from '../components/SocialMediaBlock';
    import TextBlock from '../components/TextBlock';
    import FilesBlock from '../components/FilesBlock';
    import ProductsBlock from '../components/ProductsBlock';
    import RelatedArticlesBlock from '../components/RelatedArticlesBlock';
    import InformationBlock from '../components/InformationBlock';
    import ResourceBlock from '../components/ResourcesBlock';
    import BackCounter from '../mixins/BackCounter';
    import BackIcon from '../components/BackIcon';
    import GalleryBlock from '../components/GalleryBlock';
    import CallToActionBlock from '../components/CallToActionBlock';

    export default {
        mixins: [UpdateLanguage, BackCounter],

        components: {
            CallToActionBlock,
            ResourceBlock,
            SocialMediaBlock,
            EmbedBlock,
            HeaderDesktopBlock,
            SubTitleBlock,
            FavBlock,
            MapBlock,
            BodyBlock,
            HeaderMobileBlock,
            InformationBlock,
            TextBlock,
            FilesBlock,
            ProductsBlock,
            RelatedArticlesBlock,
            BackIcon,
            GalleryBlock,
        },

        computed: {
            ...mapGetters([
                'getActiveLang',
                'getCurrentDeviceType',
                'getUserLogged',
            ]),
        },

        data() {
            return {
                eventsInfo: undefined,
                BlockTypes: ENUMS.BLOCKTYPES,
            };
        },

        created() {
            this.getEvent();
        },

        destroyed() {
            this.destroyVariables();
            this.$store.dispatch('footerVisibility', false);
        },

        methods: {
            getEvent() {
                const user = this.getUserLogged;
                if (user !== null) {
                    axios.get(CONSTANTS.apiUrl.concat('/GetEventDetailByUrl?siteId=', CONSTANTS.siteId, '&language=', this.getActiveLang, '&url=/', this.getActiveLang, this.$route.fullPath), { headers: { Authorization: 'Bearer '.concat(user.tk) } })
                        .then((response) => {
                            this.eventsInfo = response.data;
                            this.$store.dispatch('footerVisibility', true);
                        });
                } else {
                    axios.get(CONSTANTS.apiUrl.concat('/GetEventDetailByUrl?siteId=', CONSTANTS.siteId, '&language=', this.getActiveLang, '&url=/', this.getActiveLang, this.$route.fullPath))
                        .then((response) => {
                            this.eventsInfo = response.data;
                            this.$store.dispatch('footerVisibility', true);
                        });
                }
            },

            showMobile() {
                return this.getCurrentDeviceType <= ENUMS.DEVICES.TABLET;
            },

            showDesktop() {
                return this.getCurrentDeviceType === ENUMS.DEVICES.DESKTOP;
            },

            onLanguageUpdated() {
                this.getEvent();
            },

            destroyVariables() {
                this.eventsInfo = null;
                this.BlockTypes = null;
            },
        },
    };

</script>



