<template>
    <div id="cookie-div" class="cookie" v-if="isCookieAgreementSet === false">
        <p>{{ cookie.title }}</p>
        <p v-html="cookie.message"></p>
        <button class="cookie__buttons" v-on:click="acceptCookies()">{{ cookie.buttonText }}</button>
    </div>
</template>

<script>

    import { mapGetters } from 'vuex';
    import axios from 'axios';
    import { CONSTANTS } from '../main';
    import { createCookie } from '../helpers/Helpers';
    import UpdateLanguage from '../mixins/UpdateLanguage';

    export default {
        mixins: [UpdateLanguage],

        name: 'cookie',

        computed: mapGetters(['getActiveLang', 'isCookieAgreementSet']),

        created() {
            if (!this.isCookieAgreementSet) {
                this.getCookiesText();
            }
        },

        data() {
            return {
                cookie: {},
            };
        },

        methods: {

            acceptCookies() {
                createCookie('cw', '1', 365);
                this.$store.dispatch('cookieAgreement', 1);
            },

            getCookiesText() {
                axios.get(CONSTANTS.apiUrl.concat('/GetCookies?siteId=', CONSTANTS.siteId, '&language=', this.getActiveLang))
                    .then((response) => {
                        this.cookie = response.data;
                    });
            },

            onLanguageUpdated() {
                if (!this.isCookieAgreementSet) {
                    this.getCookiesText();
                }
            },
        },
    };
</script>



