<template>
    <div class="menu__container">
        <nav class="menu">
            <div class="row">
                <div class="col-xs-6 col-sm-6 menu__column" v-for="item in menus">
                    <ul v-for="menu in item">
                        <li class="menu__link" v-on:click="linkClicked">
                            <a :href="menu.url !== null ? convertLink(menu.url) : ''" @click.prevent="redirectLink(menu.url, menu.openInNewWindow)">{{ menu.name }}</a>
                        </li>
                        <div v-if="false">
                            <ul v-if="menu.childMenus">
                                <li v-for="child in menu.childMenus" class="menu__item" v-on:click="linkClicked">
                                    <a :href="child.url !== null ? convertLink(child.url) : ''" @click.prevent="redirectLink(child.url, child.openInNewWindow)">{{ child.name }}</a>
                                </li>
                            </ul>
                        </div>
                    </ul>
                </div>
            </div>
        </nav>
    </div>
</template>

<script>
    import { mapGetters } from 'vuex';
    import GlobalMethods from '../mixins/GlobalMethods';

    export default {

        name: 'menu-visit',

        mixins: [GlobalMethods],

        props: ['menus'],

        computed: {
            ...mapGetters([
                'isMenuOpen',
                'getLangs',
            ]),
        },

        methods: {
            linkClicked() {
                this.$emit('linkClicked');
            },
        },
    };

</script>



