<template>
    <div class="col-xs-12 text-block">
        <div class="row">
            <div class="col-md-2">
                <div class="text-block__filler"></div>
            </div>
            <div v-bind:class="{ 'col-md-10': fullSize, 'col-md-5': !fullSize }">
                <h1 v-if="title">{{ title }}</h1>
                <div class="text-block__text" v-html="text"></div>
            </div>
        </div>
    </div>
</template>

<script>

    export default {
        name: 'text-block',

        props: {
            text: { required: true },
            fullSize: { required: false },
            title: { required: false },
        },

        data() {
            return {};
        },

        mounted() {
        },

        methods: {
        },
    };

</script>



