<template>
    <div id="app">
        <cookie></cookie>
        <main>
            <header-visit :header="header" v-on:toogleMenu="toogleMenu" v-on:toogleSearch="toogleSearch"></header-visit>

            <big-container class="bigcontainer--menu">
                <menu-visit slot="menu" :menus="header.menu" v-on:linkClicked="toogleMenu"></menu-visit>
                <newsletter slot="newsletterDesktop" v-if="getCurrentDeviceType >= DEVICETYPES.DESKTOP"></newsletter>
                <newsletter slot="newsletterMobile" v-if="getCurrentDeviceType < DEVICETYPES.DESKTOP"></newsletter>
                <login-user-header slot="login" v-if="getCurrentDeviceType < DEVICETYPES.DESKTOP" :labels="header.loginUserHeader" class="menu__login--mobile"></login-user-header>
            </big-container>

            <search v-if="isSearchOpen" :searchLayer="header.searchLayer"></search>

            <transition name="default-transition">
                <router-view :key="$route.path" class="view"></router-view>
            </transition>
        </main>

        <footer>
            <transition name="default-transition">
                <footer-visit v-if="getFooterVisibility" :content="footer"></footer-visit>
            </transition>
        </footer>
    </div>
</template>

<script>

    import axios from 'axios';
    import debounce from 'lodash/debounce';

    import { mapGetters } from 'vuex';
    import { readCookie } from './helpers/Helpers';
    import { CONSTANTS, ENUMS } from './main';

    import Cookie from './components/Cookie';
    import HeaderVisit from './components/Header';
    import FooterVisit from './components/Footer';
    import BigContainer from './components/BigContainer';
    import MenuVisit from './components/MenuVisit';
    import UpdateLanguage from './mixins/UpdateLanguage';
    import Search from './components/Search';
    import Newsletter from './components/Newsletter';
    import LoginUserHeader from './components/LoginUserHeader';

    import GlobalMethods from './mixins/GlobalMethods';

    require('picturefill');

    export default {

        name: 'app',

        mixins: [UpdateLanguage, GlobalMethods],

        components: {
            Search,
            Cookie,
            HeaderVisit,
            FooterVisit,
            BigContainer,
            MenuVisit,
            Newsletter,
            LoginUserHeader,
        },

        computed: {
            ...mapGetters([
                'isMenuOpen',
                'getActiveLang',
                'isSearchOpen',
                'getCurtainAnimationTiming',
                'getFooterVisibility',
                'getCurrentDeviceType',
            ]),
        },

        watch: {
            isMenuOpen(menuOpen) {
                if (menuOpen) {
                    this.openMenu();
                } else {
                    this.closeMenu();
                }
            },
            isSearchOpen(searchOpen) {
                if (searchOpen) {
                    this.openSearch();
                } else {
                    this.closeSearch();
                }
            },
        },

        data() {
            return {
                header: {},
                footer: {},
                showFooter: false,
                DEVICETYPES: ENUMS.DEVICES,
                bodyHidden: false,
            };
        },

        created() {
            this.checkCookieLang();
            this.checkCookieAgreement();
            this.checkCookieFirstTime();
            this.resize();
            this.setEvents();
            this.getHeaderAndFooterData();
            this.checkLogin();
        },

        mounted() {
            this.cacheElements();
        },

        methods: {

            cacheElements() {
                this.$bigContainer = document.querySelector('.bigcontainer');
            },

            getHeaderAndFooterData() {
                axios.get(CONSTANTS.apiUrl.concat('GetHeaderAndFooter?siteId=', CONSTANTS.siteId, '&language=', this.getActiveLang))
                    .then((response) => {
                        this.header = response.data.header;
                        this.footer = response.data.footer;
                        this.$store.dispatch('loadLangs', this.header.languages);
                    });
            },

            checkCookieLang() {
                const lang = readCookie('lang');
                if (lang !== null) {
                    this.$store.dispatch('activeLang', lang);
                }
            },

            checkCookieFirstTime() {
                const cookieFirstTime = readCookie('ft');
                this.$store.dispatch('firstTime', cookieFirstTime !== null ? 0 : 1);
            },

            checkCookieAgreement() {
                const cookieAgreement = readCookie('cw');
                this.$store.dispatch('cookieAgreement', cookieAgreement !== null ? 1 : 0);
            },

            setEvents() {
                window.addEventListener('resize', debounce(() => this.resize(), 200));
            },

            checkLogin() {
                const token = readCookie('tk');

                if (token !== null) {
                    axios.get(CONSTANTS.apiAccountUrl.concat('GetUserInfo'), { headers: { Authorization: 'Bearer '.concat(token) } })
                    .then((response) => {
                        const user = {
                            name: response.data.Name,
                            email: response.data.Email,
                            tk: token,
                        };

                        this.$store.dispatch('userLogged', user);
                    });
                } else {
                    this.$store.dispatch('userLogged', null);
                }
            },

            /**
             * Event handler to change the global DEVICE constant based on the current resolution
             * and if the user resize the browser.
             */
            resize() {
                this.windowWidth = window.innerWidth;

                if (this.windowWidth > CONSTANTS.desktopRes) {
                    this.$store.dispatch('currentDevice', ENUMS.DEVICES.DESKTOP);
                } else if (this.windowWidth > CONSTANTS.tabletRes &&
                           this.windowWidth < CONSTANTS.desktopRes) {
                    this.$store.dispatch('currentDevice', ENUMS.DEVICES.TABLET);
                } else {
                    this.$store.dispatch('currentDevice', ENUMS.DEVICES.MOBILE);
                }
            },

            onLanguageUpdated() {
                this.getHeaderAndFooterData();
            },

            /**
             * Each link of the menu is hidden by default.
             * When the menu transition ends, a class is added to each link
             * which is a transition effect and have a delay between.
             */
            showMenuEntries() {
                setTimeout(() => {
                    const links = document.querySelectorAll('.menu__link, .menu__item');
                    links.forEach((item) => {
                        item.classList.add('menu-transition-delay');
                    });
                }, this.getCurtainAnimationTiming);
            },

            /**
             * Remove the class of each link item to restore his state.
             */
            hideMenuEntries() {
                const links = document.querySelectorAll('.menu__link, .menu__item');
                links.forEach((item) => {
                    item.classList.remove('menu-transition-delay');
                });
            },

            /**
             * The class 'in' is trigger to open and hide the menu.
             * When the menu is open, body need to have overflow:hidden activated
             */
            toggleMenuClass() {
                this.$bigContainer.classList.toggle('in');
                const body = document.body;
                body.classList.toggle('block-scroll');
                body.classList.toggle('menu-in');
            },

            /**
             * If the user click in an item and scroll inside the menu, the menu keeps
             * his scroll position when you go back to menu again.
             * To prevent that, when the menu is closed, the function waits x miliseconds to scroll to
             * the top of the div again.
             */
            scrollMenuToTop() {
                setTimeout(() => {
                    this.$bigContainer.scrollTop = 0;
                }, 400);
            },

            /**
             * This is the function triggered when the user click to open/close the menu
             * If the menu is open/close, a store property is changed and
             */
            toogleMenu() {
                if (this.isMenuOpen) {
                    this.$store.dispatch('menuOpen', false);
                } else {
                    this.$store.dispatch('menuOpen', true);
                }
            },

            openMenu() {
                this.toggleMenuClass();
                this.showMenuEntries();
                this.bodyHidden = true;
                if (this.isSearchOpen) {
                    this.$store.dispatch('searchOpen', false);
                }
            },

            closeMenu() {
                this.toggleMenuClass();
                this.hideMenuEntries();
                this.scrollMenuToTop();
                this.bodyHidden = false;
            },

            toogleSearch() {
                if (this.isSearchOpen) {
                    this.$store.dispatch('searchOpen', false);
                } else {
                    this.$store.dispatch('searchOpen', true);
                }
            },

            openSearch() {
                const body = document.body;
                body.classList.add('block-scroll');
                if (this.isMenuOpen) {
                    this.$store.dispatch('menuOpen', false);
                }
            },

            closeSearch() {
                const body = document.body;
                body.classList.remove('block-scroll');
            },
        },
    };
</script>

<style lang="scss">
    @import "./assets/sass/main.scss";
</style>



