<template>
    <div class="filters-modal">

        <div class="filters-bar" @click="showFilters">
            <p>
                <span v-if="counterAll > 0" class="badge-counter">{{ counterAll }}</span>
                <span class="text">{{ filterText }}</span>
            </p>
        </div>

        <div class="filters-wrapper">
            <div class="row">
                <div class="col-xs-12 col-lg-3 no-padding">
                    <div class="filters-motivations filter-box">
                        <h3>Motivations</h3>
                        <label class="filters-checkbox" v-for="motivation in motivations">
                            <input type="checkbox" v-model="selMotivations" :value="motivation.id" @click="countAllSelected">
                            <span class="check"></span>
                            <span class="title">{{ motivation.title }}</span>
                        </label>
                    </div>
                </div>

                <div class="col-xs-12 col-lg-3 no-padding" ref="box1">
                    <div class="filters-categories filter-box">
                        <h3>Categories</h3>
                        <div v-for="category in categories">
                            <label class="filters-checkbox">
                                <input type="checkbox" v-model="selCategores" v-on:click="countAllSelected" :value="category.id">
                                <span class="check"></span>
                            </label>
                            <span  class="title" @click="openSelectedSubcat(category.id)">
                                {{ category.title }}
                                <span class="badge-counter" v-if="category.subcatCount.length > 0">{{ category.subcatCount.length }}</span>
                            </span>

                        </div>
                    </div>
                </div>

                <div class="col-xs-12 col-lg-6 no-padding mask">
                    <div class="filters-sub-categories filter-box" ref="box2">
                        <h3>{{ titleCategory }}<strong> Sub-Categories</strong> </h3>
                        <label class="filters-buttons"  v-for="subCategory in subCategories">
                            <input type="checkbox" v-model="selSub" :value="subCategory" @click="countAllSelected(subCategory.categoryId)">
                            <span>{{ subCategory.title }}</span>
                        </label>
                    </div>
                </div>

            </div>

            <div class="row">
                <div class="bottom-bar">
                    <button @click="resetCounters">Clear Filters</button>
                    <button @click="applyFilters">Apply</button>
                </div>
            </div>
        </div>

    </div>
</template>

<script>
    import TweenMax from 'gsap/TweenMax';
    import axios from 'axios';
    import { mapGetters } from 'vuex';
    import { CONSTANTS, ENUMS } from '../main';

    export default {
        name: 'search-filters',
        data() {
            return {
                filterText: 'Filters',
                filtersWrapperOpen: false,
                motivations: [],
                categories: [],
                subCategories: [],
                selMotivations: [],
                selCategores: [],
                selSub: [],
                counterAll: '',
                subCatCounter: '',
                titleCategory: '',
                isClosed: true,
            };
        },
        created() {
            this.getMotivations();
            this.getCategories();
        },
        computed: {
            ...mapGetters([
                'getActiveLang',
                'getCurrentDeviceType',
            ]),
        },
        methods: {
            isPhone() {
                return this.getCurrentDeviceType <= ENUMS.DEVICES.TABLET;
            },
            showFilters() {
                if (!this.filtersWrapperOpen) {
                    this.filterText = 'Close Filters X';
                    TweenMax.to('.filters-wrapper', 2.5, {
                        marginTop: 0,
                        ease: Power4.easeOut,
                    });
                    this.filtersWrapperOpen = true;
                } else {
                    this.filterText = 'Filters';
                    TweenMax.to('.filters-wrapper', 2.5, {
                        marginTop: '-100vh',
                        ease: Power4.easeOut,
                    });
                    this.filtersWrapperOpen = false;
                    this.fadeOutBox();
                    this.isClosed = true;
                }
            },
            findSubCategoryById(currentId) {
                for (let i = 0; i < this.categories.length; i += 1) {
                    if (this.categories[i].id === currentId) {
                        this.subCategories = this.categories[i].subCategories;
                        this.titleCategory = this.categories[i].title;
                    }
                }
            },
            slideBox() {
                if (this.isPhone()) {
                    TweenMax.to('.filters-sub-categories', 0.8, {
                        marginTop: 0,
                        ease: Power4.easeOut,
                    });
                } else {
                    TweenMax.to('.filters-sub-categories', 0.8, {
                        left: this.$refs.box2.parentElement.offsetLeft,
                        ease: Power4.easeOut,
                    });
                }
            },
            fadeOutBox() {
                TweenMax.to('.filters-sub-categories', 0.3, {
                    opacity: 0,
                    ease: Power4.easeOut,
                });
                setTimeout(() => {
                    if (this.isPhone()) {
                        TweenMax.to('.filters-sub-categories', 0.3, {
                            marginTop: '-260px',
                            ease: Power4.easeOut,
                        });
                    } else {
                        TweenMax.to('.filters-sub-categories', 0.3, {
                            left: '-300px',
                            ease: Power4.easeOut,
                        });
                    }
                }, 300);
                setTimeout(() => {
                    TweenMax.to('.filters-sub-categories', 0.3, {
                        opacity: 1,
                        ease: Power4.easeOut,
                    });
                }, 800);
            },
            openSelectedSubcat(currentId) {
                if (this.isClosed) {
                    this.findSubCategoryById(currentId);
                    this.slideBox();
                    this.isClosed = false;
                } else if (!this.isClosed) {
                    this.fadeOutBox();
                    setTimeout(() => {
                        this.slideBox();
                        this.findSubCategoryById(currentId);
                    }, 750);
                }
            },
            /**
             * GET Motivations
             */
            getMotivations() {
                axios.get(CONSTANTS.apiUrl.concat('/GetSearchMotivationTypes?siteId=', CONSTANTS.siteId, '&language=', this.getActiveLang))
                    .then((response) => {
                        this.motivations = response.data;
                    });
            },
            /**
             * GET categories
             */
            getCategories() {
                axios.get(CONSTANTS.apiUrl.concat('/GetSearchCategoriesAndSubCategories?siteId=', CONSTANTS.siteId, '&language=', this.getActiveLang))
                    .then((response) => {
                        this.categories = response.data;
                        this.addCounterToCategories();
                    });
            },
            /**
             * Count All selected Filters
             */
            countAllSelected() {
                const $ = this;
                this.counterAll = $.selMotivations.length + $.selCategores.length + $.selSub.length;
                this.countSubCatById();
            },
            /**
             * Count subCategories by category_id
             */
            countSubCatById() {
                this.categories.forEach((item) => {
                    const category = item;
                    category.subcatCount = [];
                    for (let i = 0; i < this.selSub.length; i += 1) {
                        if (category.id === this.selSub[i].categoryId) {
                            if (!this.existsInArray(this.selSub[i].id, category.subcatCount)) {
                                category.subcatCount.push(this.selSub[i].id);
                            }
                        }
                    }
                    if (category.subcatCount.length &&
                        !this.existsInArray(category.id, this.selCategores)) {
                        this.selCategores.push(category.id);
                    }
                });
            },
            /**
            * send selected filters to Search Results Page
            */
            applyFilters() {
                const $ = this;
                const selSubIds = [];
                for (let i = 0; i < $.selSub.length; i += 1) {
                    selSubIds.push($.selSub[i].id);
                }
                this.$emit('filersApplied', $.selMotivations, $.selCategores, selSubIds);
            },
            /**
             * Reset filer counter when cleared
             */
            resetCounters() {
                this.counterAll = 0;
                this.selMotivations = [];
                this.selCategores = [];
                this.selSub = [];
                this.addCounterToCategories();
                this.fadeOutBox();
                this.isClosed = true;
            },
            addCounterToCategories() {
                for (let i = 0; i < this.categories.length; i += 1) {
                    this.categories[i].subcatCount = [];
                }
            },
            existsInArray(needle, haystack) {
                for (let i = 0; i < haystack.length; i += 1) {
                    if (needle === haystack[i]) {
                        return true;
                    }
                }
                return false;
            },
        },
    };

</script>



