<template>
    <nav class="header">
        <span v-if="getCurrentDeviceType <= DEVICETYPES.TABLET && !isMenuOpen && !isFirstTimeAtHome() && !isHome" v-on:click="back"><svg width="18" height="29" viewBox="0 0 24 35" xmlns="http://www.w3.org/2000/svg"><g fill="#000" fill-rule="evenodd"><g transform="rotate(-180 11.5 17.5)"><ellipse cx="9.87379" cy="9.78261" rx="2.3301" ry="2.34783"/><ellipse cx="3.68932" cy="3.71739" rx="3.68932" ry="3.71739"/><ellipse cx="18.68932" cy="17.71739" rx="3.68932" ry="3.71739"/></g><g transform="matrix(-1 0 0 1 24 0)"><ellipse cx="11.3301" cy="9.34783" rx="2.3301" ry="2.34783"/><ellipse cx="3.68932" cy="3.71739" rx="3.68932" ry="3.71739"/></g></g></svg></span>
        <a v-if="notInMobileMenu" v-on:click="hideModals()"><router-link class="logo" :to="{ name: headerLink ? headerLink : CONSTS.defaultLogoLink }" v-html="logo"></router-link></a>
        <language v-if="!notInMobileMenu" :languagesList="getLangs" :deviceType="DEVICETYPES.MOBILE"></language>

        <div class="header-right-side">
            <search-button class="bt" v-on:click.native="toogleSearch" v-if="notInMobileMenu && !isFirstTimeAtHome()" :text="header.searchPlaceHolderText"></search-button>
            <login-user-header v-if="!isFirstTimeAtHome() && getCurrentDeviceType == DEVICETYPES.DESKTOP" :labels="header.loginUserHeader"></login-user-header>
            <language :languagesList="getLangs" :deviceType="getCurrentDeviceType <= DEVICETYPES.TABLET && isFirstTimeAtHome() ? DEVICETYPES.MOBILE : DEVICETYPES.DESKTOP"></language>
            <svg v-if="!isMenuOpen && !isFirstTimeAtHome()" @click="toogleMenu" class="header-menu" width="46" height="46" viewBox="-6.5 -6.5 60 60" preserveAspectRatio="xMinYMin meet" xmlns="http://www.w3.org/2000/svg"><title>Group 3</title><g fill="none" fill-rule="evenodd"><circle fill="#E6E6E6" cx="23" cy="23" r="23"/><path d="M11.5 18.5h23.022M11.5 23.5h23.022M11.5 28.5h23.022" stroke="#000" stroke-linecap="square"/></g></svg>
            <svg v-if="isMenuOpen && !isFirstTimeAtHome()" @click="toogleMenu" class="header-menu" width="46" height="46" viewBox="-6.5 -6.5 60 60" xmlns="http://www.w3.org/2000/svg"><title>Group 2</title><g fill="none" fill-rule="evenodd"><circle fill="#E4E4E4" cx="23" cy="23" r="23"/><g fill="#FFF" transform="translate(12 12)"><g transform="rotate(-180 11 10)"><ellipse cx="5.981" cy="5.435" rx="1.282" ry="1.304"/><ellipse cx="2.029" cy="2.065" rx="2.029" ry="2.065"/></g><ellipse cx="5.981" cy="5.435" rx="1.282" ry="1.304"/><ellipse cx="2.029" cy="2.065" rx="2.029" ry="2.065"/><ellipse cx="11.427" cy="9.891" rx="2.777" ry="2.826"/><g transform="matrix(-1 0 0 1 22 0)"><ellipse cx="5.981" cy="5.435" rx="1.282" ry="1.304"/><ellipse cx="2.029" cy="2.065" rx="2.029" ry="2.065"/></g><g transform="matrix(1 0 0 -1 0 20)"><ellipse cx="5.981" cy="5.435" rx="1.282" ry="1.304"/><ellipse cx="2.029" cy="2.065" rx="2.029" ry="2.065"/></g></g><g fill="#000" transform="translate(12 12)"><g transform="rotate(-180 11 10)"><ellipse cx="5.981" cy="5.435" rx="1.282" ry="1.304"/><ellipse cx="2.029" cy="2.065" rx="2.029" ry="2.065"/></g><ellipse cx="5.981" cy="5.435" rx="1.282" ry="1.304"/><ellipse cx="2.029" cy="2.065" rx="2.029" ry="2.065"/><ellipse cx="11.427" cy="9.891" rx="2.777" ry="2.826"/><g transform="matrix(-1 0 0 1 22 0)"><ellipse cx="5.981" cy="5.435" rx="1.282" ry="1.304"/><ellipse cx="2.029" cy="2.065" rx="2.029" ry="2.065"/></g><g transform="matrix(1 0 0 -1 0 20)"><ellipse cx="5.981" cy="5.435" rx="1.282" ry="1.304"/><ellipse cx="2.029" cy="2.065" rx="2.029" ry="2.065"/></g></g></g></svg>
        </div>
    </nav>
</template>

<script>
    import { mapGetters } from 'vuex';
    import toggleMenuAnimation from '../helpers/Animations';
    import Language from './Language';
    import SearchButton from './SearchButton';
    import { CONSTANTS, ENUMS } from '../main';
    import LoginUserHeader from './LoginUserHeader';

    export default {

        components: { Language, SearchButton, LoginUserHeader },

        name: 'header-visit',

        props: [
            'headerLink',
            'show',
            'header',
        ],

        computed: {
            ...mapGetters([
                'isMenuOpen',
                'getLangs',
                'getActiveLang',
                'getCurrentDeviceType',
                'isSearchOpen',
                'isFirstTime',
            ]),

            logo() {
                if (typeof this.header.logoSVG !== 'undefined') {
                    if (this.getCurrentDeviceType === ENUMS.DEVICES.DESKTOP) {
                        return String(this.header.logoElementSVG).concat(String(this.header.logoSVG));
                    }
                    return this.header.logoSVG;
                }
                return '';
            },

            notInMobileMenu() {
                return this.getCurrentDeviceType === ENUMS.DEVICES.DESKTOP ||
                    (this.getCurrentDeviceType <= ENUMS.DEVICES.TABLET && !this.isMenuOpen);
            },

            isHome() {
                return this.$route.name === 'HomePageRoute' || this.$route.name === 'InspireMePageRoute';
            },
        },

        data() {
            return {
                CONSTS: CONSTANTS,
                DEVICETYPES: ENUMS.DEVICES,
            };
        },

        methods: {
            toogleMenu() {
                toggleMenuAnimation(this.$el);
                this.$emit('toogleMenu');
            },

            toogleSearch() {
                this.$emit('toogleSearch');
            },

            isFirstTimeAtHome() {
                return this.isFirstTime && (this.$route.name === 'HomePageRoute' || this.$route.name === 'InspireMePageRoute');
            },

            back() {
                const counter = this.$store.getters.getBackCounter;
                this.$store.dispatch('fromBack', true);

                if (counter === 1) {
                    this.$router.push({ name: 'HomePageRoute' });
                } else {
                    window.history.back();
                }

                this.$store.dispatch('decrementBackCounter');
            },

            hideModals() {
                if (this.isMenuOpen) {
                    this.$store.dispatch('menuOpen', false);
                }
                if (this.isSearchOpen) {
                    this.$store.dispatch('searchOpen', false);
                }
            },
        },
    };
</script>



