<template>
    <big-container class="bigcontainer--register">
        <register slot="register"></register>
    </big-container>
</template>

<script>
    import BigContainer from '../components/BigContainer';
    import Register from '../components/Register';
    import BackCounter from '../mixins/BackCounter';

    export default {
        mixins: [BackCounter],

        components: {
            BigContainer,
            Register,
        },
    };

</script>



