<template>
    <div class="fav-block">
        <favorite-icon
                :isFavorite="isFavorite"
                :animate="animateIcon"
                v-on:toggleFavorites="toggleFavorites"
                v-on:animationEnd="iconAnimationEnd"
                class="fav-block__icon"></favorite-icon>
        <div class="fav-block__text">
            <p class="fav-block__text-underline" v-on:click="toggleFavorites">{{ isFavorite ? content.isOnFavoritesText : content.addToFavoritesLinkText }}</p>
            <p class="fav-block__text-helper">{{ isFavorite ? content.unFavoriteText : content.addToFavoritesSubtitle }}</p>
        </div>
    </div>
</template>

<script>
    import { mapGetters } from 'vuex';
    import FavoriteIcon from './FavoriteIcon';

    export default {

        name: 'fav-block',

        components: { FavoriteIcon },

        computed: {
            ...mapGetters([
                'isUserLogged',
            ]),
        },

        props: {
            content: { required: true },
            isOnFavorites: { required: true },
        },

        watch: {
            isOnFavorites() {
                this.isFavorite = this.isOnFavorites;
            },
        },

        data() {
            return {
                isFavorite: this.isOnFavorites,
                animateIcon: false,
            };
        },

        methods: {
            toggleFavorites() {
                if (this.isUserLogged) {
                    this.animateIcon = true;
                    this.isFavorite = !this.isFavorite;
                    if (this.isFavorite) {
                        this.$emit('addFavorites');
                    } else {
                        this.$emit('removeFavorites');
                    }
                } else {
                    this.$router.push({ name: 'LoginPageRoute' });
                }
            },

            iconAnimationEnd() {
                this.animateIcon = false;
            },
        },
    };

</script>



