<template>
    <a class="article-link" :href="convertLink(content.detailUrl)" @click.prevent="goToView(convertLink(content.detailUrl))">
        <article class="result-card">
            <figure>
                <picture>
                    <source :srcset="constants.apiUrlMedia + content.desktopImage" media="(min-width: 515px)"/>
                    <source :srcset="constants.apiUrlMedia + content.mobileImage" media="(min-width: 320px)"/>
                    <img srcset="data:image/gif;base64,R0lGODlhAQABAAAAACH5BAEKAAEALAAAAAABAAEAAAICTAEAOw=="
                         alt="Sample pic"/>
                </picture>
            </figure>
            <div class="text">
                <h2>{{ content.title }}</h2>
                <p>{{ content.description }}</p>
            </div>
        </article>
    </a>
</template>

<script>
    import router from '../router/index';
    import { CONSTANTS, ENUMS } from '../main';

    export default {
        name: 'results-card',

        data() {
            return {
                constants: CONSTANTS,
            };
        },

        props: {
            content: { required: true },
        },

        methods: {
            convertLink: url => (url !== null ? url.substring(3) : ''),
            goToView(url) {
                router.push(url);
            },
        },
    };

</script>



