<template>
    <big-container class="bigcontainer--login">
        <login slot="login"></login>
    </big-container>
</template>

<script>

    import BigContainer from '../components/BigContainer';
    import Login from '../components/Login';
    import BackCounter from '../mixins/BackCounter';

    export default {
        mixins: [BackCounter],

        components: {
            BigContainer,
            Login,
        },
    };

</script>



