959,5.76983345" id="Path-Copy-46" transform="translate(13.000000, 4.268478) rotate(-90.000000) translate(-13.000000, -4.268478) "></path> <path name="smallFavPath" d="M24.5001598,41.7716402 C25.8806161,41.7716402 27,40.6514408 27,39.2691419 C27,37.8866831 25.8806161,36.7671233 24.5001598,36.7671233 C23.1192241,36.7671233 22,37.8866831 22,39.2691419 C22,40.6514408 23.1192241,41.7716402 24.5001598,41.7716402" id="Path-Copy-47" transform="translate(24.500000, 39.269382) rotate(-90.000000) translate(-24.500000, -39.269382) "></path> <path name="bigFavPath" d="M23.4804292,33.738022 C26.7825067,33.738022 29.4600941,31.0584839 29.4600941,27.7519989 C29.4600941,24.4451313 26.7825067,21.7671233 23.4804292,21.7671233 C20.1772052,21.7671233 17.5,24.4451313 17.5,27.7519989 C17.5,31.0584839 20.1772052,33.738022 23.4804292,33.738022" id="Path-Copy-48" transform="translate(23.480047, 27.752573) rotate(-90.000000) translate(-23.480047, -27.752573) "></path></g></g></g></g></svg>
                <span id="fav-info-text">{{ favoritesData.pageSubtitle }}</span>
            </div>
            <svg id="halfCircleDiv" class="fav-mobile__half-circle" viewBox="0 0 375 62" xmlns="http://www.w3.org/2000/svg"><title>Oval 10</title><path d="M378.61 60.81C325.18 22.532 259.7 0 188.96 0 118.525 0 53.305 22.34 0 60.32c51.5 1.18 188.96.49 188.96.49s128.54-1.31 189.65 0z" fill="#F27280" fill-rule="evenodd"/></svg>
            <div class="fav-mobile__container">
                <div class="fav-mobile__content">
                    <ul id="fav-groups">
                        <li v-for="columns in favoritesData.favoritesColumns">
                            <input type="checkbox" checked>
                            <i><svg width="18" height="29" viewBox="0 0 24 35" xmlns="http://www.w3.org/2000/svg"><g fill="#fff" fill-rule="evenodd"><g transform="rotate(-180 11.5 17.5)"><ellipse cx="9.87379" cy="9.78261" rx="2.3301" ry="2.34783"/><ellipse cx="3.68932" cy="3.71739" rx="3.68932" ry="3.71739"/><ellipse cx="18.68932" cy="17.71739" rx="3.68932" ry="3.71739"/></g><g transform="matrix(-1 0 0 1 24 0)"><ellipse cx="11.3301" cy="9.34783" rx="2.3301" ry="2.34783"/><ellipse cx="3.68932" cy="3.71739" rx="3.68932" ry="3.71739"/></g></g></svg></i>
                            <a class="fav-mobile__content-title"><h4>{{ columns.title }}</h4><span>{{ favoritesData.unFavoriteMessage }}</span></a>
                            <div class="fav-mobile__content-panel">
                                <div v-for="fav in columns.favorites" :id="'fav' + fav.url.replace(/\//g,'').replace(/\-/g,'')" class="fav-mobile__content-fav">
                                    <favorite-icon
                                        :isFavorite="fav.IsOnFavorites"
                                        :animate="fav.animate"
                                        :smaller="true"
                                        v-on:toggleFavorites="toggleFavorites(fav)"
                                        v-on:animationEnd="removeFromFavorites(fav, columns.title)"class="fav-mobile__content-fav-svg"></favorite-icon>
                                    <div class="fav-mobile__content-fav-text">
                                        <span>{{ fav.insertDateFormatted }}</span>
                                        <a v-if="fav.hasDetail" :href="fav.url !== null ? convertLink(fav.url) : ''" @click.prevent="redirectLink(fav.url, false)"><p>{{ fav.name }}</p></a>
                                        <p v-if="!fav.hasDetail">{{ fav.name }}</p>
                                    </div>
                                </div>
                            </div>
                        </li>
                    </ul>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import axios from 'axios';
    import { mapGetters } from 'vuex';
    import TweenMax from 'gsap/TweenMax';
    import findIndex from 'lodash/findIndex';
    import { CONSTANTS, ENUMS } from '../main';
    import GlobalMethods from '../mixins/GlobalMethods';
    import UpdateLanguage from '../mixins/UpdateLanguage';
    import { objectToString, smoothScrollTo } from '../helpers/Helpers';
    import FavoriteIcon from '../components/FavoriteIcon';

    export default {
        mixins: [UpdateLanguage, GlobalMethods],

        name: 'favorite-page',

        components: { FavoriteIcon },

        computed: {
            ...mapGetters([
                'getActiveLang',
                'getCurrentDeviceType',
                'getUserLogged',
            ]),
        },

        data() {
            return {
                isEmpty: false,
                desktopElToShow: undefined,
                favoritesData: undefined,
                mousedownID: -1,
            };
        },

        mounted() {
            this.getFavorites();
        },

        methods: {
            getFavorites() {
                const user = this.getUserLogged;
                axios.get(CONSTANTS.apiUrl.concat('/GetFavorites?siteId=', CONSTANTS.siteId, '&language=', this.getActiveLang), { headers: { Authorization: 'Bearer '.concat(user.tk) } })
                    .then((response) => {
                        this.favoritesData = response.data;
                        setTimeout(() => {
                            const arr = this.favoritesData.favoritesColumns;
                            for (let i = 0; i < arr.length; i += 1) {
                                const div = arr[i].title;
                                if (this.showDesktop()) {
                                    this.checkDisabledArrow(div);
                                }
                            }
                            this.startAnimations();
                        }, 250);
                    });
            },

            scrollFavContentDown(div, isDown) {
                if (this.mousedownID === -1) {
                    this.mousedownID = setInterval(() => {
                        this.scrollFavContent(div, isDown);
                    }, 200);
                }
            },

            scrollFavContentUp() {
                if (this.mousedownID !== -1) {
                    clearInterval(this.mousedownID);
                    this.mousedownID = -1;
                }
            },

            scrollFavContent(div, isDown) {
                const elmnt = this.$el.querySelector('#fav-content'.concat(this.getIdLegal(div)));
                if (isDown) {
                    smoothScrollTo(elmnt, elmnt.scrollTop + 25, 206);
                } else {
                    smoothScrollTo(elmnt, elmnt.scrollTop - 25, 206);
                }
                this.checkDisabledArrow(div);
            },

            checkDisabledArrow(div) {
                const elmnt = this.$el.querySelector('#fav-content'.concat(this.getIdLegal(div)));
                if (elmnt.scrollHeight === elmnt.clientHeight) {
                    this.$el.querySelector('#fav-up'.concat(this.getIdLegal(div))).classList.add('disabled');
                    this.$el.querySelector('#fav-down'.concat(this.getIdLegal(div))).classList.add('disabled');
                } else if (elmnt.scrollTop === (elmnt.scrollHeight - elmnt.clientHeight)
                        && elmnt.scrollTop !== 0) {
                    this.$el.querySelector('#fav-up'.concat(this.getIdLegal(div))).classList.remove('disabled');
                    this.$el.querySelector('#fav-down'.concat(this.getIdLegal(div))).classList.add('disabled');
                } else if (elmnt.scrollTop === 0) {
                    this.$el.querySelector('#fav-up'.concat(this.getIdLegal(div))).classList.add('disabled');
                    this.$el.querySelector('#fav-down'.concat(this.getIdLegal(div))).classList.remove('disabled');
                } else {
                    this.$el.querySelector('#fav-up'.concat(this.getIdLegal(div))).classList.remove('disabled');
                    this.$el.querySelector('#fav-down'.concat(this.getIdLegal(div))).classList.remove('disabled');
                }
            },

            getIdLegal: str => str.replace(' ', '').replace(/([^a-z0-9]+)/gi, ''),

            showMobile() {
                return this.getCurrentDeviceType <= ENUMS.DEVICES.TABLET;
            },

            showDesktop() {
                return this.getCurrentDeviceType === ENUMS.DEVICES.DESKTOP;
            },

            onLanguageUpdated() {
                this.getFavorites();
            },

            removeFromFavorites(fav, div) {
                const object = fav;
                object.animate = false;
                const user = this.getUserLogged;
                const params = objectToString({
                    siteId: CONSTANTS.siteId,
                    language: this.getActiveLang,
                    contentUrl: '/'.concat(fav.url),
                });
                const forceUpdateArray = this.favoritesData;
                this.favoritesData = undefined;
                this.favoritesData = forceUpdateArray;
                TweenMax.to(
                    this.$el.querySelector('#fav'.concat(this.getIdLegal(fav.url))),
                    1,
                    {
                        opacity: 0,
                        ease: Power1.easeInOut,
                        onComplete: () => {
                            this.$el.querySelector('#fav'.concat(this.getIdLegal(fav.url))).style.display = 'none';
                            axios.post(CONSTANTS.apiUrl.concat('RemoveFavorites'), params, { headers: { Authorization: 'Bearer '.concat(user.tk) } });
                            if (this.showDesktop()) {
                                this.checkDisabledArrow(div);
                            }
                        },
                        delay: 0.4,
                    },
                );
            },

            toggleFavorites(fav) {
                const object = fav;
                object.IsOnFavorites = false;
                object.animate = true;
                const forceUpdateArray = this.favoritesData;
                this.favoritesData = undefined;
                this.favoritesData = forceUpdateArray;
            },

            startAnimations() {
                if (this.showMobile()) {
                    this.$el.querySelector('#fav-heart').classList.add('hidden-display');
                    this.$el.querySelector('#fav-info-text').classList.add('hidden-display');
                    this.$el.querySelector('#fav-groups').classList.add('hidden-opacity');
                    this.$el.querySelector('.fav-mobile__container').classList.add('container-isanimating');
                    this.entryMobileAnimation();
                } else if (this.showDesktop()) {
                    this.desktopElToShow = (this.isEmpty ? '.fav-desktop__content__empty' : '.fav-desktop__content__container');
                    this.$el.querySelector('#sideFavIcon').classList.add('hidden-opacity');
                    this.$el.querySelector('#containerFavIcon').classList.remove('hidden-display');
                    this.$el.querySelector('#containerFavIcon').classList.add('favInfo-isanimating');
                    this.$el.querySelector(this.desktopElToShow).classList.add('hidden-opacity');
                    this.entryDesktopAnimation();
                }
            },

            entryDesktopAnimation() {
                const tl = new TimelineMax();
                tl.staggerTo(document.getElementsByName('miniFavPath'), 0.25, { opacity: 1, ease: Power1.easeInOut }, 0.1, 1)
                    .staggerTo(document.getElementsByName('smallFavPath'), 0.25, { opacity: 1, ease: Power1.easeInOut }, 0.1, 1)
                    .staggerTo(document.getElementsByName('mediumFavPath'), 0.25, { opacity: 1, ease: Power1.easeInOut }, 0.1, 1.2)
                    .staggerTo(document.getElementsByName('bigFavPath'), 0.25, { opacity: 1, ease: Power1.easeInOut }, 0.1, 1.2)
                    .to(this.$el.querySelector('#containerFavIcon'), 1, { scale: 1.2, ease: Power1.easeIn }, 1.5)
                    .to(this.$el.querySelector('#containerFavIcon'), 1, { scale: 1, ease: Power1.easeOut }, 2.5)
                    .to(this.$el.querySelector('#containerFavIcon'), 1, { opacity: 0, ease: Power1.easeInOut }, 3.5)
                    .to(this.$el.querySelector('#sideFavIcon'), 1, { opacity: 1, visibility: 'visible', ease: Power1.easeInOut }, 4.5)
                    .to(this.$el.querySelector(this.desktopElToShow),
                    1,
                    {
                        opacity: 1,
                        visibility: 'visible',
                        ease: Power1.easeInOut,
                        onComplete: () => {
                            this.$el.querySelector('#containerFavIcon').classList.add('hidden-display');
                        },
                    },
                    4.5);
            },

            entryMobileAnimation() {
                const heights = (document.getElementsByClassName('fav-mobile')[0].offsetHeight - 130).toString().concat('px');
                TweenMax.to(
                    this.$el.querySelector('.fav-mobile__container'),
                    1.5,
                    {
                        height: heights,
                        ease: Power1.easeInOut,
                        onComplete: () => {
                            this.$el.querySelector('#fav-heart').classList.remove('hidden-display');
                            this.$el.querySelector('#fav-heart').classList.add('favIcon-isanimating');
                            this.favEntryMobileAnimation();
                        },
                    },
                );
            },

            favEntryMobileAnimation() {
                const tl = new TimelineMax();
                tl.staggerTo(document.getElementsByName('miniFavPath'), 0.25, { opacity: 1, ease: Power1.easeInOut }, 0.1, 0.1)
                    .staggerTo(document.getElementsByName('smallFavPath'), 0.25, { opacity: 1, ease: Power1.easeInOut }, 0.1, 0.1)
                    .staggerTo(document.getElementsByName('mediumFavPath'), 0.25, { opacity: 1, ease: Power1.easeInOut }, 0.1, 0.3)
                    .staggerTo(document.getElementsByName('bigFavPath'), 0.25, { opacity: 1, ease: Power1.easeInOut }, 0.1, 0.3)
                    .to(this.$el.querySelector('#fav-heart'), 1, { scale: 1.4, ease: Power1.easeIn }, 1.5)
                    .to(this.$el.querySelector('#fav-heart'), 1, { scale: 1, ease: Power1.easeOut }, 2.5)
                    .to(this.$el.querySelector('#fav-heart'), 1, {
                        width: 100,
                        height: 86,
                        ease: Power1.easeOut,
                        onComplete: () => {
                            this.lastMobileAnimation();
                        },
                    }, 3.5);
            },

            lastMobileAnimation() {
                const tl = new TimelineMax();
                const heights = document.getElementsByClassName('fav-mobile__content')[0].offsetHeight.toString().concat('px');
                tl.to(this.$el.querySelector('#fav-heart'), 1,
                    {
                        top: 111,
                        ease: Power1.easeInOut,
                        onComplete: () => {
                            this.$el.querySelector('#fav-heart').classList.remove('favIcon-isanimating');
                        },
                    }, 0.1)
                  .to(this.$el.querySelector('.fav-mobile__container'), 1,
                    {
                        height: heights,
                        ease: Power1.easeInOut,
                    }, 0.1)
                  .to(this.$el.querySelector('#fav-groups'), 1,
                    {
                        opacity: 1,
                        visibility: 'visible',
                        ease: Power1.easeInOut,
                        onComplete: () => {
                            this.$el.querySelector('.fav-mobile__container').style.height = '';
                            this.$el.querySelector('.fav-mobile__container').style.minHeight = heights;
                            this.$el.querySelector('.fav-mobile__container').classList.remove('container-isanimating');
                            this.$el.querySelector('#fav-info-text').classList.remove('hidden-display');
                            this.$el.querySelector('#fav-info-text').classList.add('favInfo-isanimating');
                        },
                    }, 1);
            },
        },
    };

</script>



