import Vue from 'vue';
import Router from 'vue-router';

import HomePage from '../pagesComponents/HomePage';

import MotivationPage from '../pagesComponents/MotivationPage';
import LoginPage from '../pagesComponents/LoginPage';
import RegisterPage from '../pagesComponents/RegisterPage';
import UpcomingEventsPage from '../pagesComponents/UpcomingEventsPage';
import CaseStudiesPage from '../pagesComponents/CaseStudiesPage';
import GalleryPage from '../pagesComponents/GalleryPage';
import Article from '../pagesComponents/Article';
import Resource from '../pagesComponents/Resource';
import Event from '../pagesComponents/Event';
import About from '../pagesComponents/About';
import Map from '../pagesComponents/Map';
import FavoritesPage from '../pagesComponents/FavoritesPage';
import SearchResultsPage from '../pagesComponents/SearchResultsPage';
import { readCookie } from '../helpers/Helpers';

Vue.use(Router);

const routes = [
    {
        path: '/',
        name: 'HomePageRoute',
        component: HomePage,
    },

    {
        path: '/login',
        name: 'LoginPageRoute',
        component: LoginPage,
    },

    {
        path: '/register',
        name: 'RegisterPageRoute',
        component: RegisterPage,
    },

    {
        path: '/motivations/:motivation',
        name: 'MotivationsPageRoute',
        component: MotivationPage,
    },
    {
        path: '/upcoming-events',
        name: 'UpcomingEventsPageRoute',
        component: UpcomingEventsPage,
    },
    {
        path: '/case-studies',
        name: 'CaseStudiesPageRoute',
        component: CaseStudiesPage,
    },
    {
        path: '/gallery',
        name: 'GalleryPageRoute',
        component: GalleryPage,
    },
    {
        path: '/article/:article',
        name: 'ArticleRoute',
        component: Article,
    },
    {
        path: '/resource/:resource',
        name: 'ResourceRoute',
        component: Resource,
    },
    {
        path: '/upcoming-events/:event',
        name: 'EventRoute',
        component: Event,
    },
    {
        path: '/about',
        name: 'AboutRoute',
        component: About,
    },
    {
        path: '/map',
        name: 'MapRoute',
        component: Map,
    },
    {
        path: '/favorites',
        name: 'FavoritesPageRoute',
        component: FavoritesPage,
    },
    {
        path: '/search-results',
        name: 'SearchResultsPage',
        component: SearchResultsPage,
    },
];

const router = new Router({
    mode: 'history',
    routes,
    // to scroll to the top after the transition
    // scrollBehavior() {
    //     return { x: 0, y: 0 };
    // },
});

router.beforeEach((to, from, next) => {
    const token = readCookie('tk');

    if (to.name === 'LoginPageRoute' && token !== null) {
        next('/');
    } else if (to.name === 'FavoritesPageRoute' && token === null) {
        next('/login');
    } else {
        next();
    }
});

export default router;



