<script>
    import axios from 'axios';
    import { mapGetters } from 'vuex';
    import debounce from 'lodash/debounce';
    import FavoriteIcon from './FavoriteIcon';
    import { ENUMS, CONSTANTS } from '../main';
    import GlobalMethods from '../mixins/GlobalMethods';
    import { objectToString } from '../helpers/Helpers';

    export default {
        name: 'products-block',
        mixins: [GlobalMethods],

        components: { FavoriteIcon },

        props: {
            content: { required: true },
            addAllText: { required: true },
        },

        computed: {
            ...mapGetters([
                'getActiveLang',
                'getCurrentDeviceType',
                'getUserLogged',
                'isUserLogged',
            ]),
        },

        data() {
            return {
                productsTopRows: [],
                productsBottomRows: [],
                productsAllRows: [],
                inSmallerDesktop: false,
                isAllFavorite: false,
                allAnimate: false,
                constants: CONSTANTS,
            };
        },

        mounted() {
            for (let i = 0; i < this.content.relatedProducts.length; i += 1) {
                this.content.relatedProducts[i].animate = false;
            }
            if (this.content.relatedProducts.length <= 2) {
                for (let i = 0; i < this.content.relatedProducts.length; i += 1) {
                    this.productsTopRows.push({ left: this.content.relatedProducts[i] });
                }
            } else {
                const half = Math.ceil(this.content.relatedProducts.length / 2);
                for (let i = 0; i < half; i += 1) {
                    this.productsTopRows.push({ left: this.content.relatedProducts[i] });
                }
                const tmpArr = this.content.relatedProducts
                    .slice(half, this.content.relatedProducts.length);
                for (let i = 0; i < tmpArr.length; i += 1) {
                    this.productsTopRows[i].right = tmpArr[i];
                }
            }
            this.productsBottomRows = this.productsTopRows.splice(2, this.productsTopRows.length);
            this.productsAllRows = this.content.relatedProducts;
            this.checkScreenSize();
            this.checkFavorites();
            window.addEventListener('resize', debounce(() => this.checkScreenSize(), 200));
        },

        methods: {
            isEven(n) {
                return n === 0 || !!(n && !(n % 2));
            },
            checkScreenSize() {
                this.inSmallerDesktop = (window.innerWidth <= 1222);
            },
            showMobile() {
                return this.getCurrentDeviceType <= ENUMS.DEVICES.TABLET;
            },

            showDesktop() {
                return this.getCurrentDeviceType === ENUMS.DEVICES.DESKTOP && !this.inSmallerDesktop;
            },

            showDesktopSmaller() {
                return this.getCurrentDeviceType === ENUMS.DEVICES.DESKTOP && this.inSmallerDesktop;
            },

            checkFavorites() {
                this.isAllFavorite = true;
                for (let i = 0; i < this.content.relatedProducts.length; i += 1) {
                    if (!this.content.relatedProducts[i].isOnFavorites) {
                        this.isAllFavorite = false;
                        return false;
                    }
                }
                return false;
            },

            toggleFavorites(object) {
                if (this.isUserLogged) {
                    const product = object;
                    product.animate = true;
                    product.isOnFavorites = !product.isOnFavorites;
                    if (product.isOnFavorites) {
                        this.addToFavorites(product);
                    } else {
                        this.removeFromFavorites(product);
                    }
                } else {
                    this.$router.push({ name: 'LoginPageRoute' });
                }
            },

            iconAnimationEnd(object, arr) {
                const product = object;
                product.animate = false;
                const forceUpdateArray = this[arr];
                this[arr] = undefined;
                this[arr] = forceUpdateArray;
                this.checkFavorites();
            },

            toggleAllFavorites() {
                if (this.isUserLogged) {
                    this.allAnimate = true;
                    this.isAllFavorite = !this.isAllFavorite;
                    for (let i = 0; i < this.content.relatedProducts.length; i += 1) {
                        if (this.content.relatedProducts[i].isOnFavorites !== this.isAllFavorite) {
                            this.content.relatedProducts[i].animate = true;
                            this.content.relatedProducts[i].isOnFavorites = this.isAllFavorite;
                            if (this.isAllFavorite) {
                                this.addToFavorites(this.content.relatedProducts[i]);
                            } else {
                                this.removeFromFavorites(this.content.relatedProducts[i]);
                            }
                        }
                    }
                } else {
                    this.$router.push({ name: 'LoginPageRoute' });
                }
            },

            iconAnimationEndAll() {
                this.allAnimate = false;
            },

            removeFromFavorites(product) {
                const user = this.getUserLogged;
                const params = objectToString({
                    siteId: CONSTANTS.siteId,
                    language: this.getActiveLang,
                    contentUrl: product.url,
                });
                axios.post(CONSTANTS.apiUrl.concat('RemoveFavorites'), params, { headers: { Authorization: 'Bearer '.concat(user.tk) } });
            },

            addToFavorites(product) {
                const user = this.getUserLogged;
                const params = objectToString({
                    siteId: CONSTANTS.siteId,
                    language: this.getActiveLang,
                    contentUrl: product.url,
                });
                axios.post(CONSTANTS.apiUrl.concat('SaveFavorites'), params, { headers: { Authorization: 'Bearer '.concat(user.tk) } });
            },
        },
    };

</script>



