<template>
    <footer class="footer" v-if="content.footerMenu != undefined">
        <div class="row">
            <div class="col-xs-12 col-md-10 col-md-offset-1">
                <div class="row">
                    <div class="col-sm-3">
                        <div>
                            <a href="#" class="footer__logo" v-html="content.logoSVG"></a>
                            <div class="footer__social">
                                <a :href="social.link.url !== null ? convertLink(social.link.url) : ''" v-for="social in content.socialNetworks"  @click.prevent="redirectLink(social.link.url, social.link.openInNewWindow)" v-html="social.logo"></a>
                            </div>
                        </div>
                    </div>
                    <div class="col-sm-8  footer__group">
                        <div v-for="menu in content.footerMenu[1]">
                            <h2 v-if="menu.url === null">{{ menu.name }}</h2>
                            <a v-if="menu.url !== null" :href="menu.url !== null ? convertLink(menu.url) : ''" @click.prevent="redirectLink(menu.url, child.openInNewWindow)"><h2>{{ menu.name }}</h2></a>
                        </div>
                    </div>
                    
                </div>
            </div>
        </div>
    </footer>
</template>

<script>
    import GlobalMethods from '../mixins/GlobalMethods';

    export default {
        mixins: [GlobalMethods],

        name: 'footer-visit',

        props: {
            content: {
                required: true,
                type: Object,
            },
        },

        data() {
            return {
            };
        },

        methods: {},

    };

</script>



