<template>
    <div class="title-with-subtitle">
        <span class="title">{{ title }}</span>
        <span class="subtitle" v-html="subtitle"></span>
    </div>
</template>

<script>
    import { mapGetters } from 'vuex';
    import { ENUMS } from '../main';

    export default {
        name: 'title-with-subtitle',

        props: [
            'title',
            'subtitle',
        ],

        data() {
            return {
                DEVICETYPES: ENUMS.DEVICES,
            };
        },

        computed: {
            ...mapGetters([
                'getCurrentDeviceType',
            ]),
        },
    };
</script>



