<template>
    <div class="map-page">
        <div class="map-page-header">
            <back-icon backButtonText="back"></back-icon>
            <h1>Map</h1>
            <div class="map-page-fav">
                <favorite-icon
                    :isFavorite="showFavorites"
                    :animate="animateIcon"
                    :smaller="true"
                    v-on:toggleFavorites="toggleFavorites"
                    v-on:animationEnd="iconAnimationEnd" class="map-page-fav-icon"></favorite-icon>
                    <span>See my favorites</span>
            </div>
        </div>
        <div class="map-page-wrapper">
            <div class="map-page-content" ref="map">
            </div>
            <input class="map-page-search" type="text"></inpu>
        </div>
    </div>
</template>

<script>
    import axios from 'axios';
    import { mapGetters } from 'vuex';
    import findIndex from 'lodash/findIndex';
    import { CONSTANTS, ENUMS } from '../main';

    import BackIcon from '../components/BackIcon';
    import FavoriteIcon from '../components/FavoriteIcon';

    export default {
        components: {
            BackIcon,
            FavoriteIcon,
        },

        computed: {
            ...mapGetters([
                'getActiveLang',
                'getUserLogged',
                'isUserLogged',
            ]),
        },

        data() {
            return {
                showFavorites: false,
                animateIcon: false,
                mapObject: undefined,
                mapMarkers: [],
                selectedInfoWindow: undefined,
            };
        },

        mounted() {
            const mapOptions = {
                center: new google.maps.LatLng(38.701891, -9.423045),
                zoom: 13,
                zoomControl: true,
                zoomControlOptions: {
                    position: google.maps.ControlPosition.RIGHT_TOP,
                },
                mapTypeControl: false,
                fullscreenControl: true,
                streetViewControl: false,
                draggable: true,
                scrollwheel: true,
                disableDoubleClickZoom: true,
                styles: [
                    {
                        featureType: 'administrative',
                        elementType: 'all',
                        stylers: [
                            {
                                visibility: 'on',
                            },
                        ],
                    },
                    {
                        featureType: 'administrative.locality',
                        elementType: 'all',
                        stylers: [
                            {
                                weight: '1.00',
                            },
                        ],
                    },
                    {
                        featureType: 'administrative.locality',
                        elementType: 'geometry.fill',
                        stylers: [
                            {
                                visibility: 'off',
                            },
                        ],
                    },
                    {
                        featureType: 'administrative.locality',
                        elementType: 'labels.text',
                        stylers: [
                            {
                                visibility: 'on',
                            },
                            {
                                lightness: '30',
                            },
                        ],
                    },
                    {
                        featureType: 'landscape',
                        elementType: 'all',
                        stylers: [
                            {
                                color: '#e9e5dc',
                            },
                        ],
                    },
                    {
                        featureType: 'poi',
                        elementType: 'labels.text',
                        stylers: [
                            {
                                visibility: 'off',
                            },
                        ],
                    },
                    {
                        featureType: 'poi',
                        elementType: 'labels.icon',
                        stylers: [
                            {
                                visibility: 'off',
                            },
                        ],
                    },
                    {
                        featureType: 'poi.school',
                        elementType: 'all',
                        stylers: [
                            {
                                visibility: 'off',
                            },
                        ],
                    },
                    {
                        featureType: 'road',
                        elementType: 'all',
                        stylers: [
                            {
                                visibility: 'off',
                            },
                        ],
                    },
                    {
                        featureType: 'road',
                        elementType: 'geometry.fill',
                        stylers: [
                            {
                                color: '#ececec',
                            },
                            {
                                visibility: 'on',
                            },
                        ],
                    },
                    {
                        featureType: 'road.highway',
                        elementType: 'geometry.fill',
                        stylers: [
                            {
                                weight: '0.80',
                            },
                            {
                                lightness: '50',
                            },
                        ],
                    },
                    {
                        featureType: 'transit.line',
                        elementType: 'geometry.fill',
                        stylers: [
                            {
                                weight: '0.80',
                            },
                            {
                                gamma: '1.00',
                            },
                            {
                                lightness: '-20',
                            },
                        ],
                    },
                    {
                        featureType: 'water',
                        elementType: 'all',
                        stylers: [
                            {
                                color: '#b7cae5',
                            },
                        ],
                    },
                ],
            };
            this.mapObject = new google.maps.Map(this.$refs.map, mapOptions);

            addEventListener('resize', () => {
                setTimeout(() => {
                    google.maps.event.trigger(this.mapObject, 'resize');
                }, 500);
            });
        },

        methods: {
            getFavoritesMap() {
                const user = this.getUserLogged;
                axios.get(CONSTANTS.apiUrl.concat('/GetFavoritesMap?siteId=', CONSTANTS.siteId, '&language=', this.getActiveLang), { headers: { Authorization: 'Bearer '.concat(user.tk) } })
                    .then((response) => {
                        this.createMarkers(response.data);
                    });
            },

            toggleFavorites() {
                if (this.isUserLogged) {
                    this.animateIcon = true;
                    this.showFavorites = !this.showFavorites;
                    if (this.showFavorites) {
                        this.getFavoritesMap();
                    } else {
                        for (let i = 0; i < this.mapMarkers.length; i += 1) {
                            this.mapMarkers[i].setMap(null);
                        }
                        this.mapMarkers = [];
                    }
                } else {
                    this.$router.push({ name: 'LoginPageRoute' });
                }
            },

            iconAnimationEnd() {
                this.animateIcon = false;
            },

            createMarkers(array) {
                const icon = {
                    url: CONSTANTS.apiUrlMedia.concat('/assets/images/mappinpoint.svg'),
                    scaledSize: new google.maps.Size(35, 40),
                };
                const bounds = new google.maps.LatLngBounds();
                let marker;
                for (let i = 0; i < array.length; i += 1) {
                    const position = new google.maps
                        .LatLng(array[i].latitude, array[i].longitude);
                    /* eslint-disable */
                    const existPosition = findIndex(this.mapMarkers, (o) => { return o.position.lat() === position.lat() && o.position.lng() === position.lng(); });
                    /* eslint-enable */
                    if (existPosition === -1) {
                        bounds.extend(position);
                        marker = new google.maps.Marker({
                            position,
                            icon,
                            title: array[i].name,
                            map: this.mapObject,
                        });
                        this.mapMarkers.push(marker);
                    }
                }
                this.mapObject.fitBounds(bounds);
                this.setClickEvent(array);
            },

            getinfoContent(object) {
                return '<div class="map-page-infoWindow"><div class="map-page-infoWindow__img"><img src="'
                .concat(
                    CONSTANTS.apiUrlMedia
                    , '/assets/images/mappinpoint.svg" width="30" height="30"></div><div class="map-page-infoWindow__content"><p class="title">'
                    , object.name
                    , '</p><p class="summary">'
                    , object.summary
                    , '</p></div></div>');
            },

            setClickEvent(array) {
                for (let i = 0; i < this.mapMarkers.length; i += 1) {
                    const marker = this.mapMarkers[i];
                    let infoContent = '';
                    for (let ind = 0; ind < array.length; ind += 1) {
                        const position = new google.maps
                            .LatLng(array[ind].latitude, array[ind].longitude);
                        if (marker.position.lat() === position.lat() &&
                            marker.position.lng() === position.lng()) {
                            infoContent += this.getinfoContent(array[ind]);
                        }
                    }
                    const infoWindow = new google.maps.InfoWindow({
                        content: infoContent,
                    });
                    marker.addListener('click', () => {
                        if (this.selectedInfoWindow !== undefined) {
                            this.selectedInfoWindow.close();
                            if (this.selectedInfoWindow === infoWindow) {
                                this.selectedInfoWindow = undefined;
                                return;
                            }
                        }
                        this.selectedInfoWindow = infoWindow;
                        this.selectedInfoWindow.open(this.mapObject, marker);
                    });
                }
            },
        },
    };

</script>



