<script>
    import axios from 'axios';
    import { mapGetters } from 'vuex';
    import findIndex from 'lodash/findIndex';
    import GlobalMethods from '../mixins/GlobalMethods';
    import { ENUMS, CONSTANTS } from '../main';
    import { objectToString } from '../helpers/Helpers';
    import GalleryBlock from './GalleryBlock';
    import FavoriteIcon from './FavoriteIcon';

    export default {

        name: 'resource-block',

        mixins: [GlobalMethods],

        components: {
            FavoriteIcon,
            GalleryBlock,
        },

        props: {
            content: {
                required: true,
                type: Object,
            },

            seeLocation: {
                required: true,
            },
        },

        computed: {
            ...mapGetters([
                'getActiveLang',
                'getCurrentDeviceType',
                'getUserLogged',
                'isUserLogged',
            ]),
        },

        data() {
            return {
                isFavorite: this.content.isOnFavorites,
                animateIcon: false,
                constants: CONSTANTS,
                device: ENUMS.DEVICES,
            };
        },

        mounted() {},

        methods: {
            toggleFavorites() {
                if (this.isUserLogged) {
                    this.animateIcon = true;
                    this.isFavorite = !this.isFavorite;
                    if (this.isFavorite) {
                        this.addToFavorites();
                    } else {
                        this.removeFromFavorites();
                    }
                } else {
                    this.$router.push({ name: 'LoginPageRoute' });
                }
            },

            iconAnimationEnd() {
                this.animateIcon = false;
            },

            removeFromFavorites() {
                const user = this.getUserLogged;

                const params = objectToString({
                    siteId: CONSTANTS.siteId,
                    language: this.getActiveLang,
                    contentUrl: this.content.url,
                });

                axios.post(CONSTANTS.apiUrl.concat('RemoveFavorites'), params, { headers: { Authorization: 'Bearer '.concat(user.tk) } });
            },

            addToFavorites() {
                const user = this.getUserLogged;

                const params = objectToString({
                    siteId: CONSTANTS.siteId,
                    language: this.getActiveLang,
                    contentUrl: this.content.url,
                });

                axios.post(CONSTANTS.apiUrl.concat('SaveFavorites'), params, { headers: { Authorization: 'Bearer '.concat(user.tk) } });
            },

            showMobile() {
                return this.getCurrentDeviceType <= ENUMS.DEVICES.TABLET;
            },

            showDesktop() {
                return this.getCurrentDeviceType === ENUMS.DEVICES.DESKTOP && !this.inSmallerDesktop;
            },
        },

    };

</script>



