<template>
    <div id="searchContainer" class="search-container">
        <div class="row">
            <div class="search-container__layer">
                <form class="search-container__form" @submit.prevent="doSearch">
                    <input id="searchInput" class="search-container__input" type="text"
                        :placeholder="searchLayer.searchPlaceHolderText"
                        v-model="searchQuery" @keyup.esc="closeSearch"
                        autocomplete="off" autocorrect="off" autocapitalize="off" spellcheck="false"/>
                   <p class="search-container__text">
                       <span class="search-container__text--error" v-if="showRequiredThreeCharsMessage">{{ searchLayer.searchRequiredThreeCharsMessage }}</span>
                       <span class="search-container__text--info" v-if="getCurrentDeviceType == DEVICETYPES.DESKTOP">{{ searchLayer.searchInfoText }}</span>
                   </p>
                    <button v-if="getCurrentDeviceType <= DEVICETYPES.TABLET" type="button" class="search-container__button" @submit:prevent="doSearch">{{ searchLayer.searchButtonText }}</button>
                </form>
            </div>
        </div>
    </div>
</template>

<script>

    import { mapGetters } from 'vuex';
    import { ENUMS, CONSTANTS } from '../main';

    export default {
        name: 'search',

        props: ['searchLayer'],

        computed: {
            ...mapGetters([
                'getCurrentDeviceType',
            ]),
        },

        data() {
            return {
                DEVICETYPES: ENUMS.DEVICES,
                searchQuery: '',
                showRequiredThreeCharsMessage: false,
                results: [],
            };
        },

        methods: {
            closeSearch() {
                this.$store.dispatch('searchOpen', false);
            },

            doSearch() {
                if (this.searchQuery.length < 3) {
                    this.showRequiredThreeCharsMessage = true;
                } else {
                    this.showRequiredThreeCharsMessage = false;
                    /**
                     * Router redirect with query
                     * @param searchQuery
                     */
                    document.body.classList.remove('block-scroll');
                    this.closeSearch();
                    this.$router.push({
                        path: '/search-results',
                        query: {
                            siteId: CONSTANTS.siteId,
                            language: this.getActiveLang,
                            searchTerm: this.searchQuery,
                        },
                    });
                }
            },
        },

        mounted() {
            setTimeout(() => {
                const searchInput = document.getElementById('searchInput').focus();
                if (searchInput != null) {
                    searchInput.getElementById('searchInput').focus();
                }
            }, 600);
        },
    };

</script>



