<template>
    <div class="social-media-block" v-bind:class="{'social-media-block__inline': isInline}">
        <h2>{{ content.shareSocialNetworksIntroText }}</h2>
        <div id="addThisParentDiv" v-html="content.addThisHTML"></div>
    </div>
</template>

<script>
    import { CONSTANTS } from '../main';

    export default {
        name: 'social-media-block',

        props: {
            content: { required: true },
            seo: { required: true },
            isInline: { required: false },
        },

        data() {
            return {};
        },

        mounted() {
            const addThisDiv = document.getElementById('addThisParentDiv').firstChild;
            addThisDiv.setAttribute('data-title', this.seo.openGraphTitle);
            addThisDiv.setAttribute('data-description', this.seo.openGraphDescription);
            addThisDiv.setAttribute('data-media', CONSTANTS.apiUrlMedia.concat(this.seo.openGraphImage));
            const addThisScript = this.content.addThisScript;
            window.addthis_reload = () => {
                let insertScript;
                if (!window.addthis) {
                    window.addthis_config = { data_track_addressbar: false };
                    const s = document.createElement('script');
                    s.type = 'text/javascript';
                    s.async = true;
                    s.src = addThisScript;
                    const x = document.getElementsByClassName('social-media-block')[0];
                    insertScript = x.parentNode.insertBefore(s, x);
                } else {
                    window.addthis_share.url = window.location.href;
                    window.addthis_share.title = window.document.title;
                    window.addthis.toolbox('.addthis_toolbox');
                    window.addthis.layers.refresh();
                }
                return insertScript;
            };
            window.addthis_reload();
        },

        methods: {},
    };

</script>



