<template>
    <div class="container login in">
        <div class="row center-lg">

            <div class="col-xs-12 col-md-5">
                <h2 class="login__title">{{ login.loginTitle }}</h2>
                <h5 class="login__intro-text">{{ login.loginIntroText }}</h5>
                <input class="login__input" type="email" :placeholder="login.emailLabel" v-model="email" v-on:keyup.enter="makeLogin()">
                <input class="login__input" type="password" :placeholder="login.passwordLabel" v-model="passwd" v-on:keyup.enter="makeLogin()">
                <p class="errorMessage" v-if="showError">{{ login.loginErrorMessage }}</p>
                <p class="login__forgot-password" @click.prevent="redirectLink(login.forgotPasswordLink.url, false)">{{ login.forgotPasswordLink.title }}</p>
                <button class="login__button" v-on:click="makeLogin()">{{ login.loginLink.title }}</button>
            </div>

            <div class=" col-xs-12 col-md-5 col-md-offset-2 ">
                <h2 class="login__title">{{ login.registerTitle }}</h2>
                <h5 class="login__intro-text">{{ login.registerIntroText }}</h5>
                <button class="login__button" @click.prevent="redirectLink(login.registerLink.url, false)">{{ login.registerLink.title }}</button>

                <div class=" login__divider-sign-in">
                    <p class="login__or-text">{{ login.orText }}</p>
                    <hr class="login__divider-hr">
                    <div class="login__button login__button-social login__button-social-google">
                        <div class="login__button-social-svg"><svg width="36" height="21" viewBox="0 0 36 21" xmlns="http://www.w3.org/2000/svg"><title>Group 3</title><g fill="#FFF" fill-rule="evenodd"><path d="M.007 10.513C-.21 5.1 4.797.097 10.52.033c2.918-.236 5.755.836 7.963 2.586-.906.94-1.827 1.873-2.81 2.746-1.944-1.118-4.284-1.97-6.555-1.214-3.664.988-5.882 5.083-4.527 8.477 1.124 3.542 5.677 5.487 9.245 4 1.847-.628 3.065-2.242 3.6-3.976-2.118-.04-4.236-.015-6.353-.07-.005-1.193-.01-2.382-.005-3.575 3.53-.005 7.067-.015 10.603.015.218 2.922-.238 6.05-2.244 8.395-2.748 3.35-7.825 4.33-11.954 3.018-4.384-1.363-7.57-5.544-7.475-9.925M29.192 6h3.15c.006.997.016 2 .02 2.997 1.055.01 2.114.015 3.167.02v2.988l-3.167.015c-.01 1.002-.015 2-.02 3.002-1.054-.005-2.108 0-3.156 0-.01-1.002-.01-2-.02-2.997-1.055-.01-2.114-.015-3.167-.02V9.017c1.053-.005 2.107-.01 3.166-.02.005-.997.016-2 .026-2.997"></path></g></svg></div>
                        <a :href="login.googleLink.url">
                            {{ login.googleLink.title }}
                        </a>
                    </div>
                    <div class="login__button login__button-social login__button-social-facebook">
                        <div class="login__button-social-svg"><svg width="36" height="21" viewBox="0 0 10 18" xmlns="http://www.w3.org/2000/svg"><title>ic_facebook</title><path d="M5.62 3.89c0-1.745 3.044-.85 3.044-.85L9.06.33S7.496-.07 5.62.01C1.754.176 1.89 3.224 1.89 3.224v2.663H0V8.64h1.89v9.225h3.73V8.64h2.834l.218-2.754H5.62V3.89" fill="#FFF" fill-rule="evenodd"/></svg></div>
                        <a :href="login.facebookLink.url">
                            {{ login.facebookLink.title }}
                        </a>
                    </div>
                </div>
            </div>


        </div>
    </div>
</template>

<script>

    import { mapGetters } from 'vuex';
    import axios from 'axios';
    import findIndex from 'lodash/findIndex';
    import { CONSTANTS } from '../main';
    import UpdateLanguage from '../mixins/UpdateLanguage';
    import { objectToString, createCookie } from '../helpers/Helpers';
    import GlobalMethods from '../mixins/GlobalMethods';


    export default {
        mixins: [UpdateLanguage, GlobalMethods],

        name: 'login',

        computed: mapGetters(['getActiveLang']),

        created() {
            this.getLoginText();
        },

        data() {
            return {
                CONSTANTS: {},
                lang: '',
                login: {
                    loginLink: {},
                    forgotPasswordLink: {},
                    registerLink: {},
                    googleLink: {},
                    facebookLink: {},
                },
                email: '',
                passwd: '',
                showError: false,
            };
        },

        methods: {

            getLoginText() {
                axios.get(CONSTANTS.apiUrl.concat('/GetLoginPage?siteId=', CONSTANTS.siteId, '&language=', this.getActiveLang))
                    .then((response) => {
                        this.login = response.data;
                    });
            },

            makeLogin() {
                const params = objectToString({
                    username: this.email,
                    password: this.passwd,
                    grant_type: 'password',
                });

                axios.post(CONSTANTS.apiUrlMedia.concat('/token'), params)
                .then((response) => {
                    const user = {
                        name: response.data.displayName,
                        email: response.data.userName,
                        tk: response.data.access_token,
                    };

                    this.$store.dispatch('userLogged', user);
                    this.$router.push({ name: 'HomePageRoute' });
                    createCookie('tk', response.data.access_token);
                })
                .catch((error) => {
                    this.showError = true;
                    console.log(error);
                });
            },

            onLanguageUpdated() {
                this.getLoginText();
            },
        },
    };

</script>



