<template>
    <div class="newsletter">
        <h2 class="newsletter__title">Newsletter</h2>
        <div class="newsletter__form">
            <input v-model="email" type="email" placeholder="keep me updated" class="newsInpunt">
            <button @click.prevent="activateNewsletter" class="newsBtn">{{ buttonSend }}</button>

        </div>
        <br><br>
        <p v-if="showErrorEmpty" style="color:red; font-size:11px;">Field Email is require</p>
        <p v-if="showError" style="color:red; font-size:11px;">Please enter a valid Email</p>
    </div>
</template>

<script>
    import TweenMax from 'gsap/TweenMax';

    export default {

        name: 'newsletter',

        data() {
            return {
                email: '',
                buttonSend: 'Send',
                showErrorEmpty: false,
                showError: false,
            };
        },

        methods: {

            // click handler to send a post, sending the email in model
            // verify if the email exists, if not show error message
            // if yes, show success message
            activateNewsletter() {
                if (this.notEmpty(this.email)) {
                    if (this.isValidEmail(this.email)) {
                        this.showErrorEmpty = false;
                        this.showError = false;
                        this.buttonSend = 'thank you';
                        TweenMax.to('.newsInpunt', 0.5, {
                            opacity: '0',
                            width: '0',
                            padding: 0,
                            ease: Linear.ease,
                        });
                        TweenMax.to('.newsBtn', 0.5, {
                            margin: '0 auto 0 auto',
                            ease: Linear.ease,
                        });
                        setTimeout(() => {
                            TweenMax.to('.newsBtn', 0.5, {
                                opacity: 0,
                                ease: Linear.ease,
                            });
                            TweenMax.to('.newsletter__title', 0.5, {
                                opacity: 0,
                                ease: Linear.ease,
                            });
                        }, 5000);
                    }
                }
            },
            notEmpty(value) {
                const valueTrimed = value.replace(/^[ ]+|[ ]+$/g, '');
                if (valueTrimed !== '') {
                    return true;
                }
                this.showErrorEmpty = true;
                return false;
            },
            isValidEmail(email) {
                if (!this.validateEmail(email)) {
                    this.showError = true;
                    return false;
                }
                return true;
            },
            validateEmail(email) {
                const regExp = /^([a-zA-Z0-9_.-])+@(([a-zA-Z0-9_.-])+\.)+([a-zA-Z0-9]{2,4})+$/;
                return regExp.test(email);
            },
        },
    };

</script>



