<template>
    <div>
        <div class="container-fluid feed">
            <page-menu v-if="show" :menu="menu" :forceRefresh="forcePageMenuRefresh"></page-menu>

            <div v-if="show" class="row feed__grid">
                <div class="feed__column feed__column--bigger col-sm-8">
                    <article-card type="horizontal" :content="articles[0]"></article-card>
                </div>
                <div class="feed__column feed__column--smaller col-sm-4">
                    <article-card type="horizontal" :content="articles[1]"></article-card>
                </div>
                <div class="feed__column feed__column--inside feed__column--bigger feed__column--taller col-sm-8">
                    <article-card type="horizontal" :content="articles[2]"
                                  class="feed__card--bigger col-xs-12"></article-card>
                    <article-card type="horizontal" :content="articles[3]" :smaller="true" class="card--inside col-xs-6"></article-card>
                </div>
                <div class="feed__column feed__column--smaller feed__column--taller col-sm-4">
                    <article-card type="horizontal" :content="articles[4]"></article-card>
                </div>
            </div>

            <div class="row feed__grid feed__grid--three">
                <div v-for="(article, index) in feedArticles" :key="index" class="feed__column col-sm-4">
                    <article-card type="horizontal" :content="article"></article-card>
                </div>
            </div>
        </div>
        <infinite-loader :show="show" :setScroll="setInfiniteScroll"></infinite-loader>
    </div>
</template>

<script>
    // import of libs
    import ScrollMagic from 'scrollmagic';
    import { mapGetters } from 'vuex';
    import axios from 'axios';
    import { CONSTANTS } from '../main';
    import UpdateLanguage from '../mixins/UpdateLanguage';
    import BackCounter from '../mixins/BackCounter';

    // import of components
    import ArticleCard from '../components/ArticleCard';
    import InfiniteLoader from '../components/InfiniteLoader';
    import PageMenu from '../components/PageMenu';

    export default {

        mixins: [UpdateLanguage, BackCounter],

        components: {
            PageMenu,
            ArticleCard,
            InfiniteLoader,
        },

        computed: {
            ...mapGetters([
                'getActiveLang',
                'isFirstTime',
            ]),
        },

        data() {
            return {
                listPageTitle: '',
                listPageDescription: '',
                articles: [],
                feedArticles: [],
                menu: [],
                start: 0,
                offset: 8,
                show: false,
                forcePageMenuRefresh: false,
            };
        },

        created() {
            this.getData();
        },

        destroyed() {
            this.destroyVariables();
            this.$store.dispatch('footerVisibility', false);
        },

        methods: {

            getData() {
                if (this.start === 0) {
                    this.getArticles(this.start, this.offset);
                } else {
                    this.getArticles(0, this.start);
                }
            },

            getArticles(start, offset) {
                axios.get(CONSTANTS.apiUrl.concat('/GetArticleList?siteId=', CONSTANTS.siteId, '&language=', this.getActiveLang, '&url=/', this.getActiveLang, this.$route.fullPath, '&start=', start, '&offset=', offset))
                    .then((response) => {
                        this.menu = response.data.menu;
                        this.setInitialContent(response);
                        this.setFeedArticles();
                        this.forcePageMenuRefresh = !this.forcePageMenuRefresh;
                    });
            },

            /**
             * Setting initial content on component data from first request
             * @param response - content from API
             */
            setInitialContent(response) {
                this.listPageTitle = response.data.listPageTitle;
                this.listPageDescription = response.data.listPageDescription;
                this.articles = response.data.articles;
                this.show = true;

                if (this.start === 0) {
                    this.start = this.offset;
                    this.offset = 9;
                }
            },

            setFeedArticles() {
                this.feedArticles = this.articles.slice(5, this.articles.length);
            },

            setInfiniteScroll() {
                this.controller = new ScrollMagic.Controller();
                this.scene = new ScrollMagic.Scene({
                    triggerElement: '#loader',
                    triggerHook: 'onEnter',
                })
                    .addTo(this.controller)
                    .on('enter', () => {
                        this.loader = document.querySelector('#loader');
                        if (this.loader !== null) {
                            if (!this.loader.classList.contains('active')) {
                                this.loader.classList.add('active');
                            }
                        }
                        // getting articles is too fast
                        setTimeout(() => {
                            this.getMoreArticles();
                        }, 500);
                    });
            },

            /**
             *  Get more articles when the user scroll and there aren't more articles in the feed.
             *  start and offset are incremented on each GET.
             */
            getMoreArticles() {
                axios.get(CONSTANTS.apiUrl.concat('/GetArticleList?siteId=', CONSTANTS.siteId, '&language=', this.getActiveLang, '&url=/', this.getActiveLang, this.$route.fullPath, '&start=', this.start, '&offset=', this.offset))
                    .then((response) => {
                        const articles = response.data.articles;

                        articles.forEach((each) => {
                            this.feedArticles.push(each);
                        });

                        this.scene.update();
                        this.increaseStartOffset();
                        this.loader.classList.remove('active');
                        this.haveMoreArticles(articles);
                    });
            },

            haveMoreArticles(articles) {
                if (articles.length === 0) {
                    this.$store.dispatch('footerVisibility', true);
                }
            },

            /**
             * The offset is always the same and is needed to increase when the
             * user reach the end of the scroll.
             */
            increaseStartOffset() {
                this.start += this.offset;
            },

            onLanguageUpdated() {
                this.getData();
            },

            destroyVariables() {
                this.controller.destroy(true);
                this.controller = null;
                this.scene = null;
                this.feedArticles = null;
                this.articles = null;
                this.feedArticles = null;
                this.menu = null;
            },
        },
    };
</script>



