<script>
    import debounce from 'lodash/debounce';
    import { mapGetters } from 'vuex';
    import { ENUMS, CONSTANTS } from '../main';

    export default {
        name: 'files-block',

        props: {
            content: { required: true },
        },

        computed: {
            ...mapGetters([
                'getCurrentDeviceType',
            ]),
        },

        data() {
            return {
                relatedFiles: this.content.relatedFiles,
                filesTopRows: [],
                filesBottomRows: [],
                inSmallerDesktop: false,
                constants: CONSTANTS,
            };
        },

        mounted() {
            if (this.relatedFiles.length <= 2) {
                this.relatedFiles.forEach((each) => {
                    this.filesTopRows.push({ left: each });
                });
            } else {
                const half = Math.ceil(this.relatedFiles.length / 2);
                for (let i = 0; i < half; i += 1) {
                    this.filesTopRows.push({ left: this.relatedFiles[i] });
                }
                const tmpArr = this.relatedFiles.slice(half, this.relatedFiles.length);
                for (let i = 0; i < tmpArr.length; i += 1) {
                    this.filesTopRows[i].right = tmpArr[i];
                }
            }
            this.filesBottomRows = this.filesTopRows.splice(2, this.filesTopRows.length);
            this.checkScreenSize();
            window.addEventListener('resize', debounce(() => this.checkScreenSize(), 200));
        },

        methods: {
            isEven(n) {
                return n === 0 || !!(n && !(n % 2));
            },
            checkScreenSize() {
                this.inSmallerDesktop = (window.innerWidth <= 1222);
            },
            showMobile() {
                return this.getCurrentDeviceType <= ENUMS.DEVICES.TABLET;
            },

            showDesktop() {
                return this.getCurrentDeviceType === ENUMS.DEVICES.DESKTOP && !this.inSmallerDesktop;
            },

            showDesktopSmaller() {
                return this.getCurrentDeviceType === ENUMS.DEVICES.DESKTOP && this.inSmallerDesktop;
            },
        },
    };

</script>



