/**
 * Scale animation when the user click the button to open the menu.
 * The button has a circle element in the svg, which is the one is scaled up and down.
 * @param el - the element clicked.
 */
export default function toggleMenuAnimation(el) {
    TweenMax.fromTo(
        el.querySelector('circle'),
        0.4,
        {
            scale: 1.3,
            ease: Expo.easeOut,
        },
        {
            scale: 1,
            ease: Expo.easeOut,
        },
    );
}



