<template>
    <div class="col-xs-12 call-to-action-block">
        <div class="row">
            <div class="col-md-6">
                <h1>{{ content.blockTitle }}</h1>
                <p class="call-to-action-block__text">{{ content.blockDescription }}</p>
                <a :href="content.buttonLink.url !== null ? convertLink(content.buttonLink.url) : ''" class="call-to-action-block__button" v-on:click.prevent="redirectLink(content.buttonLink.url, content.buttonLink.openInNewWindow)">{{ content.buttonText }}</a>
            </div>
        </div>
    </div>
</template>

<script>

    import GlobalMethods from '../mixins/GlobalMethods';

    export default {
        name: 'call-to-action-block',

        mixins: [GlobalMethods],

        props: {
            content: { required: true },
        },

        methods: {
        },
    };

</script>



