<script>
    export default {
        mounted() {
            const route = this.$route.name;
            const back = this.$store.getters.fromBack;

            if ((route !== 'InspireMePageRoute' && route !== 'HomePageRoute') && !back) {
                this.$store.dispatch('incrementBackCounter');
            }

            if (back) {
                this.$store.dispatch('fromBack', false);
            }
        },

        methods: {
            back() {
                const counter = this.$store.getters.getBackCounter;
                this.$store.dispatch('fromBack', true);

                if (counter === 1) {
                    this.$router.push({ name: 'HomePageRoute' });
                } else {
                    window.history.back();
                }

                this.$store.dispatch('decrementBackCounter');
            },
        },
    };
</script>



