<template>
    <transition
        v-on:after-enter="initAnimation"
    >
        <div v-show="show" id="loader" class="text-center">
            <svg class="loader" height="40" viewBox="0 0 135 135" xmlns="http://www.w3.org/2000/svg" fill="#fff"><path class="loader__inner" d="M67.4 58c5.6 0 10-4.5 10-10s-4.4-10-10-10-10 4.5-10 10 4.5 10 10 10zm9.5 9.4c0 5.6 4.4 10 10 10 5.4 0 10-4.4 10-10s-4.6-10-10-10c-5.6 0-10 4.5-10 10zM67.3 77c-5.5 0-10 4.4-10 10 0 5.4 4.5 10 10 10s10-4.6 10-10c0-5.6-4.4-10-10-10zM58 67.3c0-5.5-4.5-10-10-10s-10 4.5-10 10 4.5 10 10 10 10-4.4 10-10z"/><path class="loader__outer" d="M28.2 40.3c6.6 0 12-5.4 12-12s-5.4-12-12-12-12 5.4-12 12 5.4 12 12 12zM59 20.5c4.6 4.7 12.2 4.7 17 0 4.6-4.7 4.6-12.3 0-17-4.8-4.7-12.4-4.7-17 0-4.8 4.7-4.8 12.3 0 17zm35.6 7.7c0 6.6 5.4 12 12 12 6.7 0 12-5.4 12-12s-5.3-12-12-12c-6.6 0-12 5.4-12 12zm20 30.7c-4.8 4.6-4.8 12.2 0 17 4.6 4.6 12.2 4.6 16.8 0 4.7-4.8 4.7-12.4 0-17-4.6-4.8-12.2-4.8-17 0zm-7.8 35.7c-6.7 0-12 5.3-12 12 0 6.6 5.3 12 12 12s12-5.4 12-12c0-6.7-5.4-12-12-12zM76 114.5c-4.6-4.7-12.2-4.7-17 0-4.6 4.7-4.6 12.3 0 17 4.8 4.6 12.4 4.6 17 0 4.7-4.7 4.7-12.3 0-17zm-35.7-7.7c0-6.7-5.4-12-12-12s-12 5.3-12 12 5.4 12 12 12 12-5.4 12-12zM20.5 76c4.7-4.6 4.7-12.2 0-17-4.7-4.6-12.3-4.6-17 0-4.7 4.8-4.7 12.4 0 17 4.7 4.7 12.3 4.7 17 0z"/></svg>
        </div>
    </transition>
</template>

<script>

    import TweenMax from 'gsap/TweenMax';

    export default {

        name: 'infinite-loader',

        props: {
            show: {
                required: true,
                type: Boolean,
            },
            setScroll: {
                required: true,
                type: Function,
            },
        },

        methods: {
            initAnimation() {
                this.setScroll(); // function from parent
                TweenMax.to('.loader__inner', 2, {
                    rotation: -360,
                    repeat: -1,
                    transformOrigin: '50% 50%',
                    ease: Power0.easeNone,
                });
                TweenMax.to('.loader__outer', 3, {
                    rotation: 360,
                    repeat: -1,
                    transformOrigin: '50% 50%',
                    ease: Power0.easeNone,
                });
            },
        },

    };

</script>



