<template>
    <div>

        <div class="container-fluid feed case-studies">
            <title-with-subtitle :title="'Case Studies & Testimonials'"></title-with-subtitle>
            <div v-if="show" class="row feed__grid">
                <article-card class="col-xs-12 col-sm-6 col-md-3" type="horizontal" :content="caseStudies[0]" :showDate="true"></article-card>
                <div class="col-xs-6 col-md-6 feed__grid--middle">
                    <div class="row">
                        <article-card class="col-sm-12 card__half" type="vertical" :content="caseStudies[1]" :showDate="true"></article-card>
                        <article-card class="col-sm-12 card__half" type="vertical" :content="caseStudies[2]" :showDate="true"></article-card>
                    </div>
                </div>
                <article-card class="col-xs-6 col-sm-4 col-md-3" type="horizontal" :content="caseStudies[3]" :showDate="true"></article-card>

                <article-card class="col-xs-6 col-sm-4 col-md-3" type="horizontal" :content="caseStudies[4]" :showDate="true"></article-card>
                <article-card class="col-xs-6 col-sm-4 col-md-3" type="horizontal" :content="caseStudies[5]" :showDate="true"></article-card>
                <div class="col-xs-12 col-sm-4 col-md-6 feed__grid--middle">
                    <div class="row">
                        <article-card class="col-sm-12 card__half" type="vertical" :content="caseStudies[6]" :showDate="true"></article-card>
                        <article-card class="col-sm-12 card__half" type="vertical" :content="caseStudies[7]" :showDate="true"></article-card>
                    </div>
                </div>
            </div>

            <div class="row feed__grid feed__grid--three">
                <div v-for="(caseStudy, index) in feedCaseStudies" :key="index" class="feed__column col-sm-4">
                    <article-card type="horizontal" :content="caseStudy" :showDate="true"></article-card>
                </div>
            </div>
        </div>
        <infinite-loader :show="show" :setScroll="setInfiniteScroll"></infinite-loader>
    </div>
</template>

<script>
    // import of libs
    import ScrollMagic from 'scrollmagic';
    import { mapGetters } from 'vuex';
    import axios from 'axios';
    import { CONSTANTS } from '../main';
    import UpdateLanguage from '../mixins/UpdateLanguage';
    import TitleWithSubtitle from '../components/TitleWithSubtitle';

    // import of components
    import ArticleCard from '../components/ArticleCard';
    import InfiniteLoader from '../components/InfiniteLoader';
    import PageMenu from '../components/PageMenu';
    import BackCounter from '../mixins/BackCounter';

    export default {

        mixins: [UpdateLanguage, BackCounter],

        components: {
            PageMenu,
            ArticleCard,
            InfiniteLoader,
            TitleWithSubtitle,
        },

        computed: {
            ...mapGetters([
                'getActiveLang',
                'isFirstTime',
            ]),
        },

        data() {
            return {
                caseStudies: [],
                feedCaseStudies: [],
                menu: [],
                start: 0,
                offset: 9,
                show: false,
                forcePageMenuRefresh: false,
            };
        },

        created() {
            this.getData();
        },

        destroyed() {
            this.destroyVariables();
            this.$store.dispatch('footerVisibility', false);
        },

        methods: {

            getData() {
                if (this.start === 0) {
                    this.getCaseStudies(this.start, this.offset);
                } else {
                    this.getCaseStudies(0, this.start);
                }
            },

            getCaseStudies(start, offset) {
                axios.get(CONSTANTS.apiUrl.concat('/GetEventsList?siteId=', CONSTANTS.siteId, '&language=', this.getActiveLang, '&url=/', this.getActiveLang, '/upcoming-events', '&start=', start, '&offset=', offset))
                    .then((response) => {
                        this.menu = response.data.menu;
                        this.setInitialContent(response);
                        this.setFeedCaseStudies();
                        this.forcePageMenuRefresh = !this.forcePageMenuRefresh;
                    });
            },

            /**
             * Setting initial content on component data from first request
             * @param response - content from API
             */
            setInitialContent(response) {
                this.caseStudies = response.data.events;
                this.show = true;

                if (this.start === 0) {
                    this.start = this.offset;
                    this.offset = 8;
                }
            },

            setFeedCaseStudies() {
                this.feedCaseStudies = this.caseStudies.slice(8, this.caseStudies.length);
            },

            setInfiniteScroll() {
                this.controller = new ScrollMagic.Controller();
                this.scene = new ScrollMagic.Scene({
                    triggerElement: '#loader',
                    triggerHook: 'onEnter',
                })
                    .addTo(this.controller)
                    .on('enter', () => {
                        this.loader = document.querySelector('#loader');
                        if (this.loader !== null) {
                            if (!this.loader.classList.contains('active')) {
                                this.loader.classList.add('active');
                            }
                        }
                        // getting articles is too fast
                        setTimeout(() => {
                            this.getMoreCaseStudies();
                        }, 500);
                    });
            },

            /**
             *  Get more articles when the user scroll and there aren't more articles in the feed.
             *  start and offset are incremented on each GET.
             */
            getMoreCaseStudies() {
                axios.get(CONSTANTS.apiUrl.concat('/GetEventsList?siteId=', CONSTANTS.siteId, '&language=', this.getActiveLang, '&url=/', this.getActiveLang, '/upcoming-events', '&start=', this.start, '&offset=', this.offset))
                    .then((response) => {
                        const caseStudies = response.data.events;
                        caseStudies.forEach((each) => {
                            this.feedCaseStudies.push(each);
                        });

                        this.scene.update();
                        this.increaseStartOffset();
                        this.loader.classList.remove('active');
                        this.haveMoreCaseStudies(caseStudies);
                    });
            },

            haveMoreCaseStudies(caseStudies) {
                if (caseStudies.length === 0) {
                    this.$store.dispatch('footerVisibility', true);
                }
            },

            /**
             * The offset is always the same and is needed to increase when the
             * user reach the end of the scroll.
             */
            increaseStartOffset() {
                this.start += this.offset;
            },

            onLanguageUpdated() {
                this.getData();
            },

            destroyVariables() {
                this.controller.destroy(true);
                this.controller = null;
                this.scene = null;
                this.feedCaseStudies = null;
                this.caseStudies = null;
                this.feedCaseStudies = null;
                this.menu = null;
            },
        },
    };
</script>



