import 'es6-promise/auto'; // for vuex work
import Vue from 'vue';
import store from './store';
import App from './App';
import router from './router';

require('picturefill');

Vue.config.productionTip = false;

const CONSTANTS = {
    apiUrl: 'http://visitcascais.azurewebsites.net/umbraco/Api/VisitContent/',
    apiAccountUrl: 'http://visitcascais.azurewebsites.net/umbraco/Api/Account/',
    apiUrlMedia: 'http://visitcascais.azurewebsites.net',
    defaultLogoLink: 'HomePageRoute',
    siteId: '1233',
    desktopRes: 1024,
    tabletRes: 768,
    mobileRes: 320,
};

// App Enums
const ENUMS = {
    DEVICES: { DESKTOP: 3, TABLET: 2, MOBILE: 1 },
    BLOCKTYPES: {
        RELATEDFILES: 'relatedFilesBlock',
        RELATEDPRODUCTS: 'blockRelatedProducts',
        RELATEDCONTENTS: 'blockRelatedContents',
        EMBED: 'blockEmbed',
        RESOURCES: 'blockResources',
        TEXT: 'blockText',
        PROPERTIES: 'blockProperties',
        CALLTOACTION: 'blockCallToAction',
        GALLERY: 'blockGallery',
    },
};

export { CONSTANTS, ENUMS };

/* eslint-disable no-new */
new Vue({
    el: '#app',
    router,
    store,
    template: '<App/>',
    components: { App },
});



