<template>
    <div class="details-article" v-if="articlesInfo != undefined">
        <div v-if="showDesktop()">
            <header-desktop-block :content="{title: articlesInfo.title, image: articlesInfo.coverImageDetailUrl }" style="height: 350px"></header-desktop-block>
            <div class="container-fluid">
                <div class="row">
                    <div class="col-md-1">
                        <back-icon v-on:back="back" :backButtonText="articlesInfo.sharedDetailInformation.backButtonText"></back-icon>
                    </div>
                    <div class="col-md-10">
                        <div class="row details-article__body details-article__block">
                            <div class="col-md-2" style="position: relative;">
                                <sub-title-block :subTitle="articlesInfo.subtitle"></sub-title-block>
                                <social-media-block :content="articlesInfo.sharedDetailInformation" :seo="articlesInfo.seo" class="details-article-desktop__social-media"></social-media-block>
                            </div>
                            <div class="col-md-6">
                                <body-block :content="articlesInfo" class="details-article-desktop__body-text"></body-block>
                            </div>
                            <div class="col-md-4 details-article__body-right-col">
                                <fav-block
                                    :content="articlesInfo.sharedDetailInformation"
                                    :isOnFavorites="articlesInfo.isOnFavorites"
                                    v-on:addFavorites="addToFavorites"
                                    v-on:removeFavorites="removeFromFavorites" class="details-article-desktop__fav">
                                </fav-block>
                                <map-block :mapPoints="articlesInfo.mapPoints" class="details-article-desktop__map"></map-block>
                            </div>
                        </div>
                        <!-- Begin Blocks-->
                        <div class="row details-article__block" v-for="block in articlesInfo.blocks">
                            <embed-block v-if="block.blockDocType === BlockTypes.EMBED" :url="block.embedContent"></embed-block>
                            <text-block v-if="block.blockDocType === BlockTypes.TEXT" :text="block.text" :fullSize="block.fillFullSpace" :title="block.blockTitle"></text-block>
                            <related-articles-block v-if="block.blockDocType === BlockTypes.RELATEDCONTENTS" :content="block"></related-articles-block>
                            <products-block v-if="block.blockDocType === BlockTypes.RELATEDPRODUCTS" :content="block" :addAllText="articlesInfo.sharedDetailInformation.addAllToFavoritesLinkText"></products-block>
                            <files-block v-if="block.blockDocType === BlockTypes.RELATEDFILES" :content="block"></files-block>
                            <resource-block v-if="block.blockDocType === BlockTypes.RESOURCES" :content="block.resource" :seeLocation="articlesInfo.sharedDetailInformation.seeLocationText"></resource-block>
                            <properties-block v-if="block.blockDocType === BlockTypes.PROPERTIES" :content="block" :fullSize="block.fillFullSpace"></properties-block>
                            <call-to-action-block v-if="block.blockDocType === BlockTypes.CALLTOACTION" :content="block" ></call-to-action-block>
                            <div class="col-md-10 col-md-offset-1">
                                <gallery-block v-if="block.blockDocType === BlockTypes.GALLERY" :images="block.images"></gallery-block>
                            </div>
                        </div>
                        <!-- End Blocks-->
                    </div>
                    <div class="col-md-1"></div>
                </div>
            </div>
        </div>

        <div v-if="showMobile()">
            <header-mobile-block
                    :content="{title: articlesInfo.title, image: articlesInfo.mobileCoverImageDetailUrl, subtitle: articlesInfo.subtitle, unFavoriteText: articlesInfo.sharedDetailInformation.unFavoriteText,
                    addToFavoritesSubtitle: articlesInfo.sharedDetailInformation.addToFavoritesSubtitle, isOnFavorites: articlesInfo.isOnFavorites,}"
                    :hasFavHelper="true"
                    v-on:addFavorites="addToFavorites"
                    v-on:removeFavorites="removeFromFavorites"></header-mobile-block>
            <div class="container-fluid">
                <div class="row">
                    <div class="col-xs-12">
                        <div class="row details-article__block">
                            <div class="col-xs-12">
                                <body-block :content="articlesInfo"></body-block>
                            </div>
                        </div>
                        <div class="row details-article__block">
                            <div class="col-xs-12">
                                <div class="details-article-mobile__social-media">
                                    <social-media-block :content="articlesInfo.sharedDetailInformation" :isInline="true" :seo="articlesInfo.seo"></social-media-block>
                                </div>
                            </div>
                        </div>
                        <!-- Begin Blocks-->
                        <div class="row details-article__block" v-for="block in articlesInfo.blocks">
                            <embed-block v-if="block.blockDocType === BlockTypes.EMBED" :url="block.embedContent"></embed-block>
                            <text-block v-if="block.blockDocType === BlockTypes.TEXT" :text="block.text" :fullSize="true" :title="block.blockTitle"></text-block>
                            <related-articles-block v-if="block.blockDocType === BlockTypes.RELATEDCONTENTS" :content="block"></related-articles-block>
                            <products-block v-if="block.blockDocType === BlockTypes.RELATEDPRODUCTS" :content="block" :addAllText="articlesInfo.sharedDetailInformation.addAllToFavoritesLinkText"></products-block>
                            <files-block v-if="block.blockDocType === BlockTypes.RELATEDFILES" :content="block"></files-block>
                            <resource-block v-if="block.blockDocType === BlockTypes.RESOURCES" :content="block.resource" :seeLocation="articlesInfo.sharedDetailInformation.seeLocationText"></resource-block>
                            <properties-block v-if="block.blockDocType === BlockTypes.PROPERTIES" :content="block" :fullSize="true"></properties-block>
                            <call-to-action-block v-if="block.blockDocType === BlockTypes.CALLTOACTION" :content="block" ></call-to-action-block>
                            <gallery-block v-if="block.blockDocType === BlockTypes.GALLERY" :images="block.images"></gallery-block>
                        </div>
                        <!-- End Blocks-->
                        <div class="row">
                            <div class="col-xs-12">
                                <map-block :mapPoints="articlesInfo.mapPoints" style="height: 350px;"></map-block>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import { mapGetters } from 'vuex';
    import axios from 'axios';
    import { CONSTANTS, ENUMS } from '../main';
    import UpdateLanguage from '../mixins/UpdateLanguage';
    import { objectToString } from '../helpers/Helpers';

    import HeaderDesktopBlock from '../components/HeaderDesktopBlock';
    import HeaderMobileBlock from '../components/HeaderMobileBlock';
    import SubTitleBlock from '../components/SubTitleBlock';
    import FavBlock from '../components/FavBlock';
    import MapBlock from '../components/MapBlock';
    import BodyBlock from '../components/BodyBlock';
    import EmbedBlock from '../components/EmbedBlock';
    import SocialMediaBlock from '../components/SocialMediaBlock';
    import TextBlock from '../components/TextBlock';
    import FilesBlock from '../components/FilesBlock';
    import ProductsBlock from '../components/ProductsBlock';
    import RelatedArticlesBlock from '../components/RelatedArticlesBlock';
    import ResourceBlock from '../components/ResourcesBlock';
    import PropertiesBlock from '../components/PropertiesBlock';
    import CallToActionBlock from '../components/CallToActionBlock';
    import GalleryBlock from '../components/GalleryBlock';
    import BackIcon from '../components/BackIcon';
    import BackCounter from '../mixins/BackCounter';

    export default {
        mixins: [UpdateLanguage, BackCounter],

        components: {
            ResourceBlock,
            BackIcon,
            SocialMediaBlock,
            EmbedBlock,
            HeaderDesktopBlock,
            SubTitleBlock,
            FavBlock,
            MapBlock,
            BodyBlock,
            HeaderMobileBlock,
            TextBlock,
            FilesBlock,
            ProductsBlock,
            RelatedArticlesBlock,
            PropertiesBlock,
            CallToActionBlock,
            GalleryBlock,
        },

        computed: {
            ...mapGetters([
                'getActiveLang',
                'getCurrentDeviceType',
                'getUserLogged',
            ]),
        },

        watch: {
            getUserLogged() {
                this.getArticle();
            },
        },

        data() {
            return {
                articlesInfo: undefined,
                BlockTypes: ENUMS.BLOCKTYPES,
                deviceType: ENUMS.DEVICES,
            };
        },

        mounted() {
            this.getArticle();
        },

        destroyed() {
            this.destroyVariables();
            this.$store.dispatch('footerVisibility', false);
        },

        methods: {
            getArticle() {
                const user = this.getUserLogged;
                if (user !== null) {
                    axios.get(CONSTANTS.apiUrl.concat('/GetArticleDetailByUrl?siteId=', CONSTANTS.siteId, '&language=', this.getActiveLang, '&url=/', this.getActiveLang, this.$route.fullPath), { headers: { Authorization: 'Bearer '.concat(user.tk) } })
                        .then((response) => {
                            this.articlesInfo = response.data;
                            this.$store.dispatch('footerVisibility', true);
                        });
                } else {
                    axios.get(CONSTANTS.apiUrl.concat('/GetArticleDetailByUrl?siteId=', CONSTANTS.siteId, '&language=', this.getActiveLang, '&url=/', this.getActiveLang, this.$route.fullPath))
                        .then((response) => {
                            this.articlesInfo = response.data;
                            this.$store.dispatch('footerVisibility', true);
                        });
                }
            },

            removeFromFavorites() {
                const user = this.getUserLogged;

                const params = objectToString({
                    siteId: CONSTANTS.siteId,
                    language: this.getActiveLang,
                    contentUrl: '/'.concat(this.getActiveLang, this.$route.fullPath),
                });

                axios.post(CONSTANTS.apiUrl.concat('RemoveFavorites'), params, { headers: { Authorization: 'Bearer '.concat(user.tk) } });
            },

            addToFavorites() {
                const user = this.getUserLogged;

                const params = objectToString({
                    siteId: CONSTANTS.siteId,
                    language: this.getActiveLang,
                    contentUrl: '/'.concat(this.getActiveLang, this.$route.fullPath),
                });

                axios.post(CONSTANTS.apiUrl.concat('SaveFavorites'), params, { headers: { Authorization: 'Bearer '.concat(user.tk) } });
            },

            showMobile() {
                return this.getCurrentDeviceType <= ENUMS.DEVICES.TABLET;
            },

            showDesktop() {
                return this.getCurrentDeviceType === ENUMS.DEVICES.DESKTOP;
            },

            onLanguageUpdated() {
                this.getArticle();
            },

            destroyVariables() {
                this.articlesInfo = null;
                this.BlockTypes = null;
            },
        },
    };

</script>



