                            elementType: 'labels.text',
                            stylers: [
                                {
                                    visibility: 'on',
                                },
                                {
                                    lightness: '30',
                                },
                            ],
                        },
                        {
                            featureType: 'landscape',
                            elementType: 'all',
                            stylers: [
                                {
                                    color: '#e9e5dc',
                                },
                            ],
                        },
                        {
                            featureType: 'poi',
                            elementType: 'labels.text',
                            stylers: [
                                {
                                    visibility: 'off',
                                },
                            ],
                        },
                        {
                            featureType: 'poi',
                            elementType: 'labels.icon',
                            stylers: [
                                {
                                    visibility: 'off',
                                },
                            ],
                        },
                        {
                            featureType: 'poi.school',
                            elementType: 'all',
                            stylers: [
                                {
                                    visibility: 'off',
                                },
                            ],
                        },
                        {
                            featureType: 'road',
                            elementType: 'all',
                            stylers: [
                                {
                                    visibility: 'off',
                                },
                            ],
                        },
                        {
                            featureType: 'road',
                            elementType: 'geometry.fill',
                            stylers: [
                                {
                                    color: '#ececec',
                                },
                                {
                                    visibility: 'on',
                                },
                            ],
                        },
                        {
                            featureType: 'road.highway',
                            elementType: 'geometry.fill',
                            stylers: [
                                {
                                    weight: '0.80',
                                },
                                {
                                    lightness: '50',
                                },
                            ],
                        },
                        {
                            featureType: 'transit.line',
                            elementType: 'geometry.fill',
                            stylers: [
                                {
                                    weight: '0.80',
                                },
                                {
                                    gamma: '1.00',
                                },
                                {
                                    lightness: '-20',
                                },
                            ],
                        },
                        {
                            featureType: 'water',
                            elementType: 'all',
                            stylers: [
                                {
                                    color: '#b7cae5',
                                },
                            ],
                        },
                    ],
                };
            }
            this.modalMapOptions = {
                zoomControl: true,
                zoomControlOptions: {
                    position: google.maps.ControlPosition.RIGHT_TOP,
                },
                mapTypeControl: false,
                fullscreenControl: false,
                streetViewControl: false,
                draggable: true,
                scrollwheel: true,
                disableDoubleClickZoom: false,
                styles: [
                    {
                        featureType: 'administrative',
                        elementType: 'all',
                        stylers: [
                            {
                                visibility: 'on',
                            },
                        ],
                    },
                    {
                        featureType: 'administrative.locality',
                        elementType: 'all',
                        stylers: [
                            {
