                        ],
                    },
                    {
                        featureType: 'road',
                        elementType: 'all',
                        stylers: [
                            {
                                visibility: 'off',
                            },
                        ],
                    },
                    {
                        featureType: 'road',
                        elementType: 'geometry.fill',
                        stylers: [
                            {
                                color: '#ececec',
                            },
                            {
                                visibility: 'on',
                            },
                        ],
                    },
                    {
                        featureType: 'road.highway',
                        elementType: 'geometry.fill',
                        stylers: [
                            {
                                weight: '0.80',
                            },
                            {
                                lightness: '50',
                            },
                        ],
                    },
                    {
                        featureType: 'transit.line',
                        elementType: 'geometry.fill',
                        stylers: [
                            {
                                weight: '0.80',
                            },
                            {
                                gamma: '1.00',
                            },
                            {
                                lightness: '-20',
                            },
                        ],
                    },
                    {
                        featureType: 'water',
                        elementType: 'all',
                        stylers: [
                            {
                                color: '#b7cae5',
                            },
                        ],
                    },
                ],
            };
            this.initMapBlock(mapBlockOptions, this.showMobile());
        },

        methods: {
            initMapBlock(mapOptions, isMobile) {
                const icon = {
                    url: CONSTANTS.apiUrlMedia.concat('/assets/images/mappinpoint.svg'),
                    scaledSize: new google.maps.Size(35, 40),
                };
                const bounds = new google.maps.LatLngBounds();
                const mapObject = new google.maps.Map(this.$refs.mapBlock, mapOptions);
                let marker;
                for (let i = 0; i < this.mapPoints.length; i += 1) {
                    const position = new google.maps
                        .LatLng(this.mapPoints[i].latitude, this.mapPoints[i].longitude);
                    bounds.extend(position);
                    marker = new google.maps.Marker({
                        position,
                        icon,
                        map: mapObject,
                        title: this.mapPoints[i].title,
                    });
                    const tmpMarker = marker;
                    let infoContent;
                    if (isMobile) {
                        infoContent = '<div class="map-block__modal-container__locations"><div style="display: inline-flex"><img src="'
                            .concat(
                                CONSTANTS.apiUrlMedia
                                , '/assets/images/mappinpoint.svg" width="65" height="90"><div style="float:left;margin-left: 5px;"><p class="map-block__modal-container__locations-title">'
                                , this.mapPoints[i].title
                                , '</p><p class="map-block__modal-container__locations-address">'
                                , this.mapPoints[i].completeAddress
                                , '</p></div></div></div>');
                    } else {
                        infoContent = this.mapPoints[i].title;
                        marker.addListener('click', () => {
                            this.openModal(tmpMarker);
                        });
                    }
                    const infowindow = new google.maps.InfoWindow({
                        content: infoContent,
                    });
                    marker.addListener('mouseover', () => {
                        infowindow.open(mapObject, tmpMarker);
                    });
                    marker.addListener('mouseout', () => {
                        infowindow.close(mapObject, tmpMarker);
                    });
                    mapObject.fitBounds(bounds);
                    const listener = google.maps.event.addListener(mapObject, 'idle', () => {
                        if (this.mapPoints.length === 1) {
                            mapObject.setZoom(15);
                        } else if (mapObject.getZoom() > 11) {
                            mapObject.setZoom(11);
                        }
                        google.maps.event.removeListener(listener);
                    });
                }
                if (!isMobile) {
                    google.maps.event.addListener(mapObject, 'click', () => {
                        this.openModal();
                    });
                }
            },
            initModalMap(mapOptions, selectedMarker) {
                const icon = {
                    url: CONSTANTS.apiUrlMedia.concat('/assets/images/mappinpoint.svg'),
                    scaledSize: new google.maps.Size(35, 40),
                };
                const smallerIcon = {
                    url: CONSTANTS.apiUrlMedia.concat('/assets/images/mappinpoint.svg'),
                    scaledSize: new google.maps.Size(20, 25),
                };
                const bounds = new google.maps.LatLngBounds();
                const mapObject = new google.maps.Map(this.$refs.modalMap, mapOptions);
                let marker;
                let selectedMarkerIndex = 0;
                for (let i = 0; i < this.mapPoints.length; i += 1) {
                    const position = new google.maps
                        .LatLng(this.mapPoints[i].latitude, this.mapPoints[i].longitude);
                    bounds.extend(position);
                    marker = new google.maps.Marker({
                        position,
                        icon: smallerIcon,
                        map: mapObject,
                        title: this.mapPoints[i].title,
                    });
                    mapObject.fitBounds(bounds);
                    this.modalMarkers.push(marker);
                    const tmpMarker = marker;
                    marker.addListener('click', () => {
                        for (let ind = 0; ind < this.modalMarkers.length; ind += 1) {
                            this.modalMarkers[ind].setIcon(smallerIcon);
                        }
                        tmpMarker.setIcon(icon);
                        this.selectedMarker = this.mapPoints[i];
                    });
                    if (selectedMarker !== undefined) {
                        if (selectedMarker.position.lat() === marker.position.lat() &&
                            selectedMarker.position.lng() === marker.position.lng()) {
                            selectedMarkerIndex = i;
                        }
                    }
                }
                const listener = google.maps.event.addListener(mapObject, 'idle', () => {
                    if (this.mapPoints.length === 1) {
                        mapObject.setZoom(15);
                    }
                    google.maps.event.removeListener(listener);
                });
                this.modalMarkers[selectedMarkerIndex].setIcon(icon);
                this.selectedMarker = this.mapPoints[selectedMarkerIndex];
            },
            openModal(selectedMarker) {
                this.modalMarkers = [];
                this.selectedMarker = {};
                this.$refs.modal.classList.add('map-block__modal__open');
                this.initModalMap(this.modalMapOptions, selectedMarker);
            },
            closeModal() {
                this.$refs.modal.classList.remove('map-block__modal__open');
            },

            showMobile() {
                return this.getCurrentDeviceType <= ENUMS.DEVICES.TABLET;
            },
            showDesktop() {
                return this.getCurrentDeviceType === ENUMS.DEVICES.DESKTOP;
            },
        },
    };

</script>



