<template>
    <div v-if="isDefined(labels)" class="login-user-header">
        <div v-if="!isUserLogged" class="login-user-header_notlogged" v-on:click="hideModals()">
            <router-link :to="{ name: 'LoginPageRoute' }">{{ labels.loginLink.title }}</router-link> / <router-link :to="{ name: 'RegisterPageRoute' }">{{ labels.signInLink.title }}</router-link>
        </div>
        <div v-if="isUserLogged" class="login-user-header_logged">
            <span>{{ labels.welcomeText }}, {{ getUserLogged.name }}</span>
            <a href="#" v-on:click.prevent="makeLogout()">{{ labels.logoutLink.title }}</a>
        </div>
    </div>
</template>

<script>
    import { mapGetters } from 'vuex';
    import axios from 'axios';
    import { CONSTANTS } from '../main';
    import GlobalMethods from '../mixins/GlobalMethods';
    import { deleteCookie } from '../helpers/Helpers';

    export default {
        mixins: [GlobalMethods],

        name: 'login-user-header',

        props: [
            'labels',
        ],

        computed: {
            ...mapGetters([
                'isMenuOpen',
                'isSearchOpen',
                'isUserLogged',
                'getUserLogged',
            ]),
        },

        methods: {
            makeLogout() {
                const user = this.getUserLogged;

                if (user !== null) {
                    axios.post(CONSTANTS.apiAccountUrl.concat('Logout'), {}, { headers: { Authorization: 'Bearer '.concat(user.tk) } })
                    .then(() => {
                        deleteCookie('tk');
                        this.$store.dispatch('userLogged', null);
                        this.$router.push({ name: 'HomePageRoute' });
                    });
                }
            },

            hideModals() {
                if (this.isMenuOpen) {
                    this.$store.dispatch('menuOpen', false);
                }
                if (this.isSearchOpen) {
                    this.$store.dispatch('searchOpen', false);
                }
            },
        },
    };
</script>



