<template>
    <div class="header-block-mobile">
        <div class="header-block-mobile__img">
            <img :src="constants.apiUrlMedia + content.image" alt="">
            <favorite-icon
                    v-if="!hideFav"
                    :isFavorite="isFavorite"
                    :animate="animateIcon"
                    v-on:toggleFavorites="toggleFavorites"
                    v-on:animationEnd="iconAnimationEnd"
                    class="header-block-mobile__fav-icon"></favorite-icon>
        </div>
        <div class="header-block-mobile__text">
            <span v-if="hasFavHelper && !hideFav" class="header-block-mobile__text-fav-helper">{{ isFavorite ? content.unFavoriteText : content.addToFavoritesSubtitle }}</span>
            <svg height="40" viewBox="0 0 64 37" xmlns="http://www.w3.org/2000/svg"><title>Group 3</title><g fill-rule="evenodd"><ellipse cx="61.972" cy="9.439" rx="1.901" ry="1.888"/><ellipse cx="61.972" cy="21.395" rx="1.901" ry="1.888"/><ellipse cx="40.301" cy="9.439" rx="1.901" ry="1.888"/><ellipse cx="40.301" cy="21.395" rx="1.901" ry="1.888"/><ellipse cx="18.25" cy="9.439" rx="1.901" ry="1.888"/><ellipse cx="18.25" cy="21.395" rx="1.901" ry="1.888"/><ellipse cx="52.784" cy="9.376" rx="1.204" ry="1.196"/><ellipse cx="52.784" cy="21.332" rx="1.204" ry="1.196"/><ellipse cx="31.113" cy="9.376" rx="1.204" ry="1.196"/><ellipse cx="31.113" cy="21.332" rx="1.204" ry="1.196"/><ellipse cx="9.061" cy="9.376" rx="1.204" ry="1.196"/><ellipse cx="9.061" cy="21.332" rx="1.204" ry="1.196"/><ellipse cx="44.927" cy="9.502" rx="1.204" ry="1.196"/><ellipse cx="44.927" cy="21.457" rx="1.204" ry="1.196"/><ellipse cx="23.255" cy="9.502" rx="1.204" ry="1.196"/><ellipse cx="23.255" cy="21.457" rx="1.204" ry="1.196"/><ellipse cx="1.204" cy="9.502" rx="1.204" ry="1.196"/><ellipse cx="1.204" cy="21.457" rx="1.204" ry="1.196"/><ellipse cx="48.855" cy="9.502" rx="1" ry="1"/><ellipse cx="48.855" cy="21.457" rx="1" ry="1"/><ellipse cx="27.184" cy="9.502" rx="1" ry="1"/><ellipse cx="27.184" cy="21.457" rx="1" ry="1"/><ellipse cx="5.133" cy="9.502" rx="1" ry="1"/><ellipse cx="5.133" cy="21.457" rx="1" ry="1"/><ellipse cx="57.727" cy="9.376" rx="1.204" ry="1.196"/><ellipse cx="57.727" cy="21.332" rx="1.204" ry="1.196"/><ellipse cx="36.055" cy="9.376" rx="1.204" ry="1.196"/><ellipse cx="36.055" cy="21.332" rx="1.204" ry="1.196"/><ellipse cx="14.004" cy="9.376" rx="1.204" ry="1.196"/><ellipse cx="14.004" cy="21.332" rx="1.204" ry="1.196"/><g transform="matrix(-1 0 0 1 63.873 13.718)"><ellipse transform="matrix(-1 0 0 1 123.945 0)" cx="61.972" cy="1.888" rx="1.901" ry="1.888"/><ellipse transform="matrix(-1 0 0 1 80.602 0)" cx="40.301" cy="1.888" rx="1.901" ry="1.888"/><ellipse transform="matrix(-1