1.951" rx="1.204" ry="1.196"/><ellipse transform="matrix(-1 0 0 1 97.71 0)" cx="48.855" cy="1.951" rx="1" ry="1"/><ellipse transform="matrix(-1 0 0 1 54.368 0)" cx="27.184" cy="1.951" rx="1" ry="1"/><ellipse transform="matrix(-1 0 0 1 10.265 0)" cx="5.133" cy="1.951" rx="1" ry="1"/><ellipse transform="matrix(-1 0 0 1 115.453 0)" cx="57.727" cy="1.825" rx="1.204" ry="1.196"/><ellipse transform="matrix(-1 0 0 1 72.11 0)" cx="36.055" cy="1.825" rx="1.204" ry="1.196"/><ellipse transform="matrix(-1 0 0 1 28.008 0)" cx="14.004" cy="1.825" rx="1.204" ry="1.196"/></g><g transform="translate(23.065)"><ellipse cx="24.523" cy="3.083" rx="3.105" ry="3.083"/><ellipse cx="30.669" cy="3.146" rx="1.901" ry="1.888"/><ellipse cx="18.25" cy="3.272" rx="1.901" ry="1.888"/><ellipse cx="9.061" cy="3.209" rx="1.204" ry="1.196"/><ellipse cx="34.915" cy="3.209" rx="1.204" ry="1.196"/><ellipse cx="1.204" cy="3.335" rx="1.204" ry="1.196"/><ellipse cx="5.133" cy="3.335" rx="1" ry="1"/><ellipse cx="14.004" cy="3.209" rx="1.204" ry="1.196"/><ellipse cx="39.16" cy="3.02" rx="1.774" ry="1.762"/></g><g transform="matrix(-1 0 0 1 40.935 30.833)"><ellipse cx="24.523" cy="3.083" rx="3.105" ry="3.083"/><ellipse cx="30.669" cy="3.146" rx="1.901" ry="1.888"/><ellipse cx="18.25" cy="3.272" rx="1.901" ry="1.888"/><ellipse cx="9.061" cy="3.209" rx="1.204" ry="1.196"/><ellipse cx="34.915" cy="3.209" rx="1.204" ry="1.196"/><ellipse cx="1.204" cy="3.335" rx="1.204" ry="1.196"/><ellipse cx="5.133" cy="3.335" rx="1" ry="1"/><ellipse cx="14.004" cy="3.209" rx="1.204" ry="1.196"/><ellipse cx="39.16" cy="3.02" rx="1.774" ry="1.762"/></g></g></svg><br>
            <h1>{{ content.title }}</h1>
            <h4>{{ content.subtitle }}</h4>
        </div>
    </div>
</template>

<script>

    import { mapGetters } from 'vuex';
    import { CONSTANTS } from '../main';
    import FavoriteIcon from './FavoriteIcon';

    export default {
        name: 'header-mobile-block',
        components: { FavoriteIcon },

        computed: {
            ...mapGetters([
                'isUserLogged',
            ]),
        },

        props: {
            content: { required: true },
            hasFavHelper: { required: false },
            hideFav: { required: false },
        },

        data() {
            return {
                constants: CONSTANTS,
                isFavorite: this.content.isOnFavorites,
                animateIcon: false,
            };
        },

        mounted() {
        },

        methods: {
            toggleFavorites() {
                if (this.isUserLogged) {
                    this.animateIcon = true;
                    this.isFavorite = !this.isFavorite;
                    if (this.isFavorite) {
                        this.$emit('addFavorites');
                    } else {
                        this.$emit('removeFavorites');
                    }
                } else {
                    this.$router.push({ name: 'LoginPageRoute' });
                }
            },

            iconAnimationEnd() {
                this.animateIcon = false;
            },
        },
    };

</script>



