<template>
    <div class="details-resource" v-if="resourcesInfo !== 'undefined'">
        <div v-if="showDesktop()">
            <header-desktop-block :content="{title: resourcesInfo.title, image: resourcesInfo.coverImageDetailUrl }" style="height: 350px"></header-desktop-block>
            <div class="container-fluid">
                <div class="row">
                    <div class="col-md-1">
                        <back-icon v-on:back="back" :backButtonText="resourcesInfo.sharedDetailInformation.backButtonText"></back-icon>
                    </div>
                    <div class="col-md-10">
                        <div class="row details-resource__body details-resource__block">
                            <div class="col-md-2" style="position: relative;">
                                <sub-title-block :subTitle="resourcesInfo.subtitle"></sub-title-block>
                                <information-block
                                    :content="resourcesInfo.properties"
                                    :socialLinks="resourcesInfo.socialLinks"
                                    :address="resourcesInfo.address"></information-block>
                            </div>
                            <div class="col-md-6">
                                <body-block :content="resourcesInfo" class="details-resource-desktop__body-text"></body-block>
                            </div>
                            <div class="col-md-4 details-resource__body-right-col">
                                <fav-block
                                    :content="resourcesInfo.sharedDetailInformation"
                                    :isOnFavorites="resourcesInfo.isOnFavorites"
                                    v-on:addFavorites="addToFavorites"
                                    v-on:removeFavorites="removeFromFavorites" class="details-resource-desktop__fav"></fav-block>
                                <map-block :mapPoints="resourcesInfo.mapPoints" class="details-resource-desktop__map"></map-block>
                            </div>
                        </div>
                        <div class="row details-resource__block">
                            <div class="col-md-2"></div>
                            <div class="col-md-10">
                                <social-media-block :content="resourcesInfo.sharedDetailInformation" :seo="resourcesInfo.seo" class="details-resource-desktop__social-media" :isInline="true"></social-media-block>
                            </div>
                        </div>
                        <!-- Begin Blocks-->
                        <div class="row details-resource__block" v-for="block in resourcesInfo.blocks">
                            <embed-block v-if="block.blockDocType === BlockTypes.EMBED" :url="block.embedContent"></embed-block>
                            <text-block v-if="block.blockDocType === BlockTypes.TEXT" :text="block.text" :fullSize="block.fillFullSpace" :title="block.blockTitle"></text-block>
                            <related-articles-block v-if="block.blockDocType === BlockTypes.RELATEDCONTENTS" :content="block"></related-articles-block>
                            <products-block v-if="block.blockDocType === BlockTypes.RELATEDPRODUCTS" :content="block" :addAllText="resourcesInfo.sharedDetailInformation.addAllToFavoritesLinkText"></products-block>
                            <files-block v-if="block.blockDocType === BlockTypes.RELATEDFILES" :content="block"></files-block>
                            <resource-block v-if="block.blockDocType === BlockTypes.RESOURCES" :content="block.resource" :seeLocation="resourcesInfo.sharedDetailInformation.seeLocationText"></resource-block>
                            <properties-block v-if="block.blockDocType === BlockTypes.PROPERTIES" :content="block" :fullSize="block.fillFullSpace"></properties-block>
                            <call-to-action-block v-if="block.blockDocType === BlockTypes.CALLTOACTION" :content="block" ></call-to-action-block>
                            <gallery-block v-if="block.blockDocType === BlockTypes.GALLERY" :images="block.images"></gallery-block>
                        </div>
                        <!-- End Blocks-->
                    </div>
                    <div class="col-md-1"></div>
                </div>
            </div>
        </div>
        <div v-if="showMobile()">
            <header-mobile-block
                    :content="{title: resourcesInfo.title, image: resourcesInfo.mobileCoverImageDetailUrl, subtitle: resourcesInfo.subtitle, unFavoriteText: resourcesInfo.sharedDetailInformation.unFavoriteText,
                    addToFavoritesSubtitle: resourcesInfo.sharedDetailInformation.addToFavoritesSubtitle, isOnFavorites: resourcesInfo.isOnFavorites,}"
                    :hasFavHelper="true"
                    v-on:addFavorites="addToFavorites"
                    v-on:removeFavorites="removeFromFavorites"></header-mobile-block>
            <div class="container-fluid">
                <div class="row">
                    <div class="col-xs-12">
                        <div class="row details-article__block">
                            <div class="col-xs-12">
                                <body-block :content="resourcesInfo"></body-block>
                            </div>
                        </div>
                        <div class="row details-article__block">
                            <div class="col-xs-12">
                                <div class="details-resource-mobile__social-media">
                                    <social-media-block :content="resourcesInfo.sharedDetailInformation" :seo="resourcesInfo.seo" :isInline="true"></social-media-block>
                                </div>
                            </div>
                        </div>
                        <!-- Begin Blocks-->
                        <div class="row details-resource__block" v-for="block in resourcesInfo.blocks">
                            <embed-block v-if="block.blockDocType === BlockTypes.EMBED" :url="block.embedContent"></embed-block>
                            <text-block v-if="block.blockDocType === BlockTypes.TEXT" :text="block.text" :fullSize="true" :title="block.blockTitle"></text-block>
                            <related-articles-block v-if="block.blockDocType === BlockTypes.RELATEDCONTENTS" :content="block"></related-articles-block>
                            <products-block v-if="block.blockDocType === BlockTypes.RELATEDPRODUCTS" :content="block" :addAllText="resourcesInfo.sharedDetailInformation.addAllToFavoritesLinkText"></products-block>
                            <files-block v-if="block.blockDocType === BlockTypes.RELATEDFILES" :content="block"></files-block>
                            <resource-block v-if="block.blockDocType === BlockTypes.RESOURCES" :content="block.resource" :seeLocation="resourcesInfo.sharedDetailInformation.seeLocationText"></resource-block>
                            <properties-block v-if="block.blockDocType === BlockTypes.PROPERTIES" :content="block" :fullSize="true"></properties-block>
                            <call-to-action-block v-if="block.blockDocType === BlockTypes.CALLTOACTION" :content="block" ></call-to-action-block>
                            <gallery-block v-if="block.blockDocType === BlockTypes.GALLERY" :images="block.images"></gallery-block>
                        </div>
                        <!-- End Blocks-->
                        <div class="row details-resource__block">
                            <div class="col-xs-12">
                                <information-block :content="resourcesInfo.properties" :address="resourcesInfo.address"></information-block>
                            </div>
                        </div>
                        <div class="row">
                            <div class="col-xs-12">
                                <map-block :mapPoints="resourcesInfo.mapPoints" style="height: 350px;"></map-block>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import { mapGetters } from 'vuex';
    import axios from 'axios';
    import { CONSTANTS, ENUMS } from '../main';
    import UpdateLanguage from '../mixins/UpdateLanguage';
    import { objectToString } from '../helpers/Helpers';

    import HeaderDesktopBlock from '../components/HeaderDesktopBlock';
    import HeaderMobileBlock from '../components/HeaderMobileBlock';
    import SubTitleBlock from '../components/SubTitleBlock';
    import FavBlock from '../components/FavBlock';
    import MapBlock from '../components/MapBlock';
    import BodyBlock from '../components/BodyBlock';
    import EmbedBlock from '../components/EmbedBlock';
    import SocialMediaBlock from '../components/SocialMediaBlock';
    import TextBlock from '../components/TextBlock';
    import FilesBlock from '../components/FilesBlock';
    import ProductsBlock from '../components/ProductsBlock';
    import RelatedArticlesBlock from '../components/RelatedArticlesBlock';
    import InformationBlock from '../components/InformationBlock';
    import ResourceBlock from '../components/ResourcesBlock';
    import GalleryBlock from '../components/GalleryBlock';
    import BackIcon from '../components/BackIcon';
    import BackCounter from '../mixins/BackCounter';
    import CallToActionBlock from '../components/CallToActionBlock';

    export default {
        mixins: [UpdateLanguage, BackCounter],

        components: {
            CallToActionBlock,
            ResourceBlock,
            BackIcon,
            SocialMediaBlock,
            EmbedBlock,
            HeaderDesktopBlock,
            SubTitleBlock,
            FavBlock,
            MapBlock,
            BodyBlock,
            HeaderMobileBlock,
            InformationBlock,
            TextBlock,
            FilesBlock,
            ProductsBlock,
            RelatedArticlesBlock,
            GalleryBlock,
        },

        computed: {
            ...mapGetters([
                'getActiveLang',
                'getCurrentDeviceType',
                'getUserLogged',
            ]),
        },

        data() {
            return {
                resourcesInfo: undefined,
                BlockTypes: ENUMS.BLOCKTYPES,
            };
        },

        created() {
            this.getResource();
        },

        destroyed() {
            this.destroyVariables();
            this.$store.dispatch('footerVisibility', false);
        },

        methods: {
            getResource() {
                const user = this.getUserLogged;
                if (user !== null) {
                    axios.get(CONSTANTS.apiUrl.concat('/GetResourceDetailByUrl?siteId=', CONSTANTS.siteId, '&language=', this.getActiveLang, '&url=/', this.getActiveLang, this.$route.fullPath), { headers: { Authorization: 'Bearer '.concat(user.tk) } })
                        .then((response) => {
                            this.resourcesInfo = response.data;
                            this.$store.dispatch('footerVisibility', true);
                        });
                } else {
                    axios.get(CONSTANTS.apiUrl.concat('/GetResourceDetailByUrl?siteId=', CONSTANTS.siteId, '&language=', this.getActiveLang, '&url=/', this.getActiveLang, this.$route.fullPath))
                        .then((response) => {
                            this.resourcesInfo = response.data;
                            this.$store.dispatch('footerVisibility', true);
                        });
                }
            },

            removeFromFavorites() {
                const user = this.getUserLogged;
                const params = objectToString({
                    siteId: CONSTANTS.siteId,
                    language: this.getActiveLang,
                    contentUrl: '/'.concat(this.getActiveLang, this.$route.fullPath),
                });
                axios.post(CONSTANTS.apiUrl.concat('RemoveFavorites'), params, { headers: { Authorization: 'Bearer '.concat(user.tk) } });
            },

            addToFavorites() {
                const user = this.getUserLogged;
                const params = objectToString({
                    siteId: CONSTANTS.siteId,
                    language: this.getActiveLang,
                    contentUrl: '/'.concat(this.getActiveLang, this.$route.fullPath),
                });
                axios.post(CONSTANTS.apiUrl.concat('SaveFavorites'), params, { headers: { Authorization: 'Bearer '.concat(user.tk) } });
            },

            showMobile() {
                return this.getCurrentDeviceType <= ENUMS.DEVICES.TABLET;
            },

            showDesktop() {
                return this.getCurrentDeviceType === ENUMS.DEVICES.DESKTOP;
            },

            onLanguageUpdated() {
                this.getResource();
            },

            destroyVariables() {
                this.resourcesInfo = null;
                this.BlockTypes = null;
            },
        },
    };

</script>



