<template>
    <a class="btn"
       :href="convertLink(content.url)"
       :style="{ backgroundColor: content.motivations[0].color }"
       @click.prevent="showClickText"
       :data-hover="content.buttonToDetailHoverText"
       :data-click="content.buttonToDetailClickText"
    >
        <span class="btn--initial">{{ content.buttonToDetailText }}</span>
    </a>
</template>

<script>

    import router from '../router/index';

    export default {

        name: 'round-btn',

        props: {
            content: {
                required: true,
                type: Object,
            },
        },

        methods: {

            convertLink: url => (url !== null ? url.substring(3) : ''),

            showClickText() {
                this.$el.classList.add('clicked');
                setTimeout(() => {
                    router.push(this.convertLink(this.content.url));
                }, 600);
            },

        },

    };

</script>



