<script>
    export default {

        methods: {
            isDefined(parent) {
                return typeof parent !== 'undefined';
            },

            redirectLink(url, newWindow) {
                if (url !== null && url !== 'undefined') {
                    if (newWindow) {
                        window.open(this.convertLink(url), '_blank');
                    } else if (this.convertLink(url)[0] === '/') {
                        this.$router.push(this.convertLink(url));
                    } else {
                        window.open(this.convertLink(url), '_self');
                    }
                }
            },

            convertLink(url) {
                let result = url;

                if (result[0] === '/') {
                    if (result[3] === '/') {
                        result = url.substring(3, url.length);
                    }
                } else if (result[2] === '/') {
                    result = url.substring(2, url.length);
                }

                return result;
            },
        },
    };
</script>



