<template>
    <div class="slider resources-block-desktop__img">
        <img :src="constants.apiUrlMedia + currentImage">
        <span class="nav-arrows prev" @click="previous">
            <span class="arrow"></span>
        </span>
        <span class="nav-arrows next" @click="next">
            <span class="arrow"></span>
        </span>

        <div class="nav-dots">
            <span class="dot" v-for="(index, dot) in images"  @click="dotSlide(dot)" v-bind:class="{ active: dot === currentIndex }"></span>
        </div>
    </div>

</template>

<script>

    import { CONSTANTS } from '../main';

    export default {

        name: 'gallery-block',

        props: ['images', 'type'],

        data() {
            return {
                sliderSize: this.images.length,
                isSlider: this.sliderSize > 1,
                currentIndex: 0,
                currentImage: '',
                isActive: true,
                constants: CONSTANTS,
            };
        },

        mounted() {
            this.currentImage = this.images[this.currentIndex];
        },

        methods: {
            previous() {
                if (this.currentIndex - 1 >= 0) {
                    this.currentIndex -= 1;
                } else {
                    this.currentIndex = this.sliderSize - 1;
                }

                this.currentImage = this.images[this.currentIndex];
            },

            next() {
                if (this.currentIndex + 1 < this.sliderSize) {
                    this.currentIndex += 1;
                } else {
                    this.currentIndex = 0;
                }

                this.currentImage = this.images[this.currentIndex];
            },

            dotSlide(dot) {
                this.currentImage = this.images[dot];
                this.currentIndex = dot;
            },
        },
    };

</script>



