1.204" cy="1.951" rx="1.204" ry="1.196"/><ellipse transform="matrix(-1 0 0 1 97.71 0)" cx="48.855" cy="1.951" rx="1" ry="1"/><ellipse transform="matrix(-1 0 0 1 54.368 0)" cx="27.184" cy="1.951" rx="1" ry="1"/><ellipse transform="matrix(-1 0 0 1 10.265 0)" cx="5.133" cy="1.951" rx="1" ry="1"/><ellipse transform="matrix(-1 0 0 1 115.453 0)" cx="57.727" cy="1.825" rx="1.204" ry="1.196"/><ellipse transform="matrix(-1 0 0 1 72.11 0)" cx="36.055" cy="1.825" rx="1.204" ry="1.196"/><ellipse transform="matrix(-1 0 0 1 28.008 0)" cx="14.004" cy="1.825" rx="1.204" ry="1.196"/></g><g transform="translate(23.065)"><ellipse cx="24.523" cy="3.083" rx="3.105" ry="3.083"/><ellipse cx="30.669" cy="3.146" rx="1.901" ry="1.888"/><ellipse cx="18.25" cy="3.272" rx="1.901" ry="1.888"/><ellipse cx="9.061" cy="3.209" rx="1.204" ry="1.196"/><ellipse cx="34.915" cy="3.209" rx="1.204" ry="1.196"/><ellipse cx="1.204" cy="3.335" rx="1.204" ry="1.196"/><ellipse cx="5.133" cy="3.335" rx="1" ry="1"/><ellipse cx="14.004" cy="3.209" rx="1.204" ry="1.196"/><ellipse cx="39.16" cy="3.02" rx="1.774" ry="1.762"/></g><g transform="matrix(-1 0 0 1 40.935 30.833)"><ellipse cx="24.523" cy="3.083" rx="3.105" ry="3.083"/><ellipse cx="30.669" cy="3.146" rx="1.901" ry="1.888"/><ellipse cx="18.25" cy="3.272" rx="1.901" ry="1.888"/><ellipse cx="9.061" cy="3.209" rx="1.204" ry="1.196"/><ellipse cx="34.915" cy="3.209" rx="1.204" ry="1.196"/><ellipse cx="1.204" cy="3.335" rx="1.204" ry="1.196"/><ellipse cx="5.133" cy="3.335" rx="1" ry="1"/><ellipse cx="14.004" cy="3.209" rx="1.204" ry="1.196"/><ellipse cx="39.16" cy="3.02" rx="1.774" ry="1.762"/></g></g></svg>
                    <div>
                        <h1>{{ content.title }}</h1>
                        <p v-if="getCurrentDeviceType > DEVICETYPES.TABLET && !smaller" v-html="content.resume"></p>
                        <round-btn v-if="getCurrentDeviceType > DEVICETYPES.TABLET" :content="content" class="hover"></round-btn>
                    </div>
                </figcaption>
            </figure>
            <span class="date" v-if="isDefined(showDate)">{{ content.startDate.substring(0,10) }} - {{ content.endDate.substring(0,10) }}</span>
        </article>
    </transition>
</template>

<script>

    import ScrollMagic from 'scrollmagic';

    import { mapGetters } from 'vuex';
    import TweenMax from 'gsap';
    import { CONSTANTS, ENUMS } from '../main';
    import GlobalMethods from '../mixins/GlobalMethods';
    import router from '../router/index';

    // import of components
    import RoundBtn from './RoundButton';

    export default {

        name: 'article-card',

        mixins: [GlobalMethods],

        components: { RoundBtn },

        computed: {
            ...mapGetters([
                'getCurrentDeviceType',
            ]),
        },

        data() {
            return {
                constants: CONSTANTS,
                DEVICETYPES: ENUMS.DEVICES,
                buttonContent: null,
                info: {},
            };
        },

        props: {
            type: { required: true },
            content: { required: true },
            smaller: { type: Boolean },
            showDate: { required: false },
        },

        methods: {
            animateCard() {
                const controller = new ScrollMagic.Controller();

                new ScrollMagic.Scene({
                    triggerElement: this.$el,
                    triggerHook: 'onEnter',
                    reverse: false,
                })
                    .on('enter', () => {
                        TweenMax.to(
                            this.$el,
                            0.4,
                            {
                                autoAlpha: 1,
                                y: 0,
                                ease: Expo.easeOut,
                            },
                        );
                    })
                    .addTo(controller);
            },

            goToView(url) {
                this.redirectLink(url, false);
            },

        },

    };
</script>



