import Vue from 'vue';
import VueMediaEmbed from 'vue-media-embed';
import Vuex from 'vuex';

Vue.use(Vuex);

const store = new Vuex.Store({
    state: {
        langsList: [],
        activeLang: 'en',
        cookieAgreement: 0,
        firstTime: 0,
        user: null, /* {
            name: 'Ricardo Malveiro',
        }, */
        currentDeviceType: -1,
        menuOpen: false,
        searchOpen: false,
        showFooter: false,
        curtainAnimationTiming: 1, // 1 second
        backCounter: 0,
        back: false,
    },
    actions: {

        loadLangs({ commit }, list) {
            commit('setLangs', list);
        },

        activeLang({ commit }, lang) {
            commit('setActiveLang', lang);
        },

        currentDevice({ commit }, deviceType) {
            commit('setCurrentDeviceType', deviceType);
        },

        cookieAgreement({ commit }, value) {
            commit('setCookieAgreement', value);
        },

        menuOpen({ commit }, isOpen) {
            commit('setMenuOpen', isOpen);
        },

        searchOpen({ commit }, isOpen) {
            commit('setSearchOpen', isOpen);
        },

        firstTime({ commit }, isFirstTime) {
            commit('setFirstTime', isFirstTime);
        },

        footerVisibility({ commit }, visible) {
            commit('setFooterVisibility', visible);
        },

        incrementBackCounter({ commit }) {
            commit('incrementBackCounter');
        },

        decrementBackCounter({ commit }) {
            commit('decrementBackCounter');
        },

        fromBack({ commit }, fromback) {
            commit('fromBack', fromback);
        },

        userLogged({ commit }, user) {
            commit('setUserLogged', user);
        },
    },
    mutations: {

        /* eslint-disable no-param-reassign */
        setLangs(state, langsList) {
            state.langsList = langsList;
        },

        setActiveLang(state, activeLang) {
            state.activeLang = activeLang;
        },

        setCurrentDeviceType(state, deviceType) {
            state.currentDeviceType = deviceType;
        },

        setCookieAgreement(state, value) {
            state.cookieAgreement = value;
        },

        setMenuOpen(state, isOpen) {
            state.menuOpen = isOpen;
        },

        setSearchOpen(state, isOpen) {
            state.searchOpen = isOpen;
        },

        setFirstTime(state, isFirstTime) {
            state.firstTime = isFirstTime;
        },

        setFooterVisibility(state, visible) {
            state.showFooter = visible;
        },

        incrementBackCounter(state) {
            state.backCounter += 1;
        },

        decrementBackCounter(state) {
            state.backCounter -= 1;
        },

        setFromBack(state, fromback) {
            state.back = fromback;
        },

        setUserLogged(state, user) {
            state.user = user;
        },
    },
    getters: {

        getLangs(state) {
            return state.langsList;
        },

        getActiveLang(state) {
            return state.activeLang.toLowerCase();
        },

        getCurrentDeviceType(state) {
            return state.currentDeviceType;
        },

        isCookieAgreementSet(state) {
            return state.cookieAgreement === 1;
        },

        isUserLogged(state) {
            return state.user !== null;
        },

        getUserLogged(state) {
            return state.user;
        },

        isMenuOpen(state) {
            return state.menuOpen;
        },

        isSearchOpen(state) {
            return state.searchOpen;
        },

        getCurtainAnimationTiming(state) {
            return state.curtainAnimationTiming;
        },

        isFirstTime(state) {
            return state.firstTime === 1;
        },

        getFooterVisibility(state) {
            return state.showFooter;
        },

        getBackCounter(state) {
            return state.backCounter;
        },

        fromBack(state) {
            return state.back;
        },
    },
});

Vue.use(VueMediaEmbed, { store });

export default store;



