<script>
    import axios from 'axios';
    import { mapGetters } from 'vuex';
    import { CONSTANTS, ENUMS } from '../main';
    import BackCounter from '../mixins/BackCounter';

    import SearchFilters from '../components/SearchFilters';
    import ResultsCard from '../components/ResultsCard';

    export default {
        mixins: [BackCounter],
        components: {
            SearchFilters,
            ResultsCard,
        },
        props: ['searchLayer'],
        computed: {
            ...mapGetters([
                'getActiveLang',
            ]),
        },
        created() {
            this.getEvent();
        },
        data() {
            return {
                DEVICETYPES: ENUMS.DEVICES,
                eventResult: [],
                searchQuery: this.$route.query.searchTerm,
                showRequiredThreeCharsMessage: false,
                motivations: [],
                categories: [],
                subCategories: [],
            };
        },
        methods: {
            doSearch() {
                if (this.searchQuery.length < 3) {
                    this.showRequiredThreeCharsMessage = true;
                } else {
                    this.showRequiredThreeCharsMessage = false;
                    /**
                     * Router redirect with query
                     * @param searchQuery
                     */
                    this.$router.push({
                        path: '/search-results',
                        query: {
                            siteId: CONSTANTS.siteId,
                            language: this.getActiveLang,
                            searchTerm: this.searchQuery,
                        },
                    });
                    this.getEvent(this.motivations, this.categories, this.subCategories);
                }
            },
            getEvent(motID = [], catID = [], subID = []) {
                axios.post(CONSTANTS.apiUrl.concat('Search?siteId=', this.$route.query.siteId, '&language=', this.getActiveLang, '&searchTerm=', this.$route.query.searchTerm, '&start=0&offset=8', '&searchFilters=', { motivations: motID, categories: catID, subCategories: subID }))
                    .then((response) => {
                        this.eventResult = response.data;
                    })
                    .catch((error) => {
                        console.log(error);
                    });
            },
            filterSearch(motivations, categories, subcategories) {
                this.motivations = motivations;
                this.categories = categories;
                this.subCategories = subcategories;
            },

        },

    };

</script>



