<template>
    <ul class="page-menu">
        <li v-for="item in menu">
            <a :href="item.url !== null ? convertLink(item.url) : ''" @click.prevent="redirectLink(item.url, item.openInNewWindow)" :class="{active: item.isSelected}">{{ item.name }}</a>
        </li>
    </ul>
</template>

<script>

    import router from '../router/index';
    import GlobalMethods from '../mixins/GlobalMethods';

    export default {

        name: 'page-menu',

        mixins: [GlobalMethods],

        props: {
            menu: {
                required: true,
                type: Array,
            },
            forceRefresh: { required: true },
        },

        watch: {
            forceRefresh() {
                this.setSelectedInMiddle();
            },
        },

        mounted() {
            this.setSelectedInMiddle();
        },

        methods: {
            setSelectedInMiddle() {
                const selected = this.menu.find(x => x.isSelected);
                const selectedIndex = this.menu.indexOf(selected);
                const middle = Math.round(this.menu.length / 2) - 1;

                this.menu.splice(middle, 0, this.menu.splice(selectedIndex, 1)[0]);
            },
        },

    };

</script>



