<template>
    <div class="col-xs-12 properties-block">
        <div class="row">
            <div class="col-md-2">
                <div class="properties-block__filler"></div>
            </div>
            <div v-bind:class="{ 'col-md-10': fullSize, 'col-md-5': !fullSize }">
                <h1>{{ content.blockTitle }}</h1>
                <p>{{ content.blockDescription }}</p>
                <div class="row properties-block__content">
                    <div class="col-xs-5" v-for="properties in content.properties" v-bind:class="{ 'col-sm-4': fullSize, 'col-sm-6': !fullSize }">
                        <h4>{{ properties.label }}</h4>
                        <span>{{ properties.value }}</span>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>

    export default {
        name: 'propreties-block',

        props: {
            content: { required: true },
            fullSize: { required: false },
        },
        methods: {},
    };

</script>



