<template>
    <div>
        <div id='LanguageSelect' class="language-select"
             v-if="showDesktop()">
            <div v-on:click="openMenu()" class="language-select__selected">
                {{ getActiveLang.toUpperCase() }}
                <svg width="10" height="6" viewBox="0 0 10 6" xmlns="http://www.w3.org/2000/svg"><title>Path 10</title>
                    <path d="M1 0l4.5 5.5 4-5.5" stroke="#979797" fill="none" fill-rule="evenodd"></path>
                </svg>
            </div>
            <div id='LanguageMenu' class="language-select__menu">
                <div v-for="item in languagesList" class="language-select__item" v-on:click="changeLanguage(item)">
                    {{ item.toUpperCase() }}
                </div>
            </div>
        </div>

        <div id='LanguageSelectHorizontal' class="language-select-horizontal"
             v-if="showMobile()">
            <span v-on:click="openMenuHorizontal()" class="language-select-horizontal__selected">
                {{ getActiveLang.toUpperCase() }}
            </span>
            <span id='LanguageMenuHorizontal' class="language-select-horizontal__menu">
                <span v-for="(item, index) in languagesList" class="language-select-horizontal__item" v-on:click="changeLanguage(item)">
                    {{ item.toUpperCase() }}<span v-if="!isLast(languagesList, index)"> / </span>
                </span>
            </span>
        </div>
    </div>
</template>

<script>

    import { mapGetters } from 'vuex';
    import { ENUMS } from '../main';
    import { createCookie, hasParent } from '../helpers/Helpers';

    export default {

        name: 'language',

        props: ['languagesList', 'deviceType'],

        computed: mapGetters(['getActiveLang', 'getCurrentDeviceType']),

        mounted() {
            document.addEventListener('click', (ev) => {
                const target = ev.target;
                const select = document.getElementById('LanguageSelect');
                const menu = document.getElementById('LanguageMenu');
                if (menu != null) {
                    if (target !== select &&
                        !hasParent(target, select) &&
                        this.showDesktop()) {
                        menu.classList.remove('language-select__menu__open');
                    }
                }
                const selectHorizontal = document.getElementById('LanguageSelectHorizontal');
                const menuHorizontal = document.getElementById('LanguageMenuHorizontal');
                if (menuHorizontal != null) {
                    if (target !== selectHorizontal &&
                        !hasParent(target, selectHorizontal) &&
                        this.showMobile()) {
                        menuHorizontal.classList.remove('language-select-horizontal__menu__open');
                    }
                }
            });
        },

        data() {
            return {
                selectedLanguage: '',
            };
        },

        methods: {
            openMenu() {
                const menu = document.getElementById('LanguageMenu');
                menu.classList.toggle('language-select__menu__open');
            },

            openMenuHorizontal() {
                const menuHorizontal = document.getElementById('LanguageMenuHorizontal');
                menuHorizontal.classList.toggle('language-select-horizontal__menu__open');
            },

            closeMenu() {
                if (this.showDesktop()) {
                    const menu = document.getElementById('LanguageMenu');
                    menu.classList.remove('language-select__menu__open');
                }
                if (this.showMobile()) {
                    const menuHorizontal = document.getElementById('LanguageMenuHorizontal');
                    menuHorizontal.classList.remove('language-select-horizontal__menu__open');
                }
            },

            changeLanguage(lang) {
                if (lang !== this.getActiveLang) {
                    createCookie('lang', lang, 365);
                    this.$store.dispatch('activeLang', lang);
                    this.closeMenu();
                } else {
                    this.closeMenu();
                }
            },

            showMobile() {
                return ((this.getCurrentDeviceType === ENUMS.DEVICES.MOBILE ||
                this.getCurrentDeviceType === ENUMS.DEVICES.TABLET) &&
                this.deviceType === ENUMS.DEVICES.MOBILE);
            },

            showDesktop() {
                return (this.getCurrentDeviceType === ENUMS.DEVICES.DESKTOP &&
                this.deviceType === ENUMS.DEVICES.DESKTOP);
            },

            isLast(arr, index) {
                return (arr.length - 1 === index);
            },
        },
    };
</script>



