<template>
    <div class="container register in" v-if="register !== undefined">
        <div class="successMessage" v-if="showSuccessMessage">
            <h1 class="register__title">Registration successfully done!</h1>
            <p>You can <span @click="goToLogin">login</span> now</p>
        </div>
        <div class="row" v-if="!showSuccessMessage">
            <div class="col-sm-1 col-lg-1"></div>
            <div class="col-xs-12 col-md-4 col-lg-4">
                <h2 class="register__title">{{ register.loginDataHeaderTitle }}</h2>
                <h5 class="register__intro-text">{{ register.loginDataHeaderSubtitle }}</h5>
                <input class="register__input" type="email" :placeholder="register.eMailLabel.concat('*')" v-model="registerData.email" v-on:keyup.enter="makeRegistration()">
                <input class="register__input" type="password" :placeholder="register.passwordLabel.concat('*')" v-model="registerData.password" v-on:keyup.enter="makeRegistration()">
                <input class="register__input" type="password" :placeholder="register.confirmPasswordLabel.concat('*')" v-model="registerData.confirmPassword" v-on:keyup.enter="makeRegistration()">
                <p class="errorMessage" v-if="showError">{{ register.requiredFieldMessage }}</p>
            </div>
            <div class="col-sm-2 col-lg-2"></div>
            <div class="col-xs-12 col-sm-4 col-lg-4">
                <h2 class="register__title">{{ register.personalInformationHeaderTitle }}</h2>
                <h5 class="register__intro-text">{{ register.personalInformationHeaderSubtitle }}</h5>
                <input class="register__input" type="text" :placeholder="register.firstNameLabel.concat('*')" v-model="registerData.firstname" v-on:keyup.enter="makeRegistration()">
                <input class="register__input" type="text" :placeholder="register.lastNameLabel.concat('*')" v-model="registerData.lastname" v-on:keyup.enter="makeRegistration()">
                <input class="register__input" type="text" :placeholder="register.birthDateLabel.concat('*')" v-model="registerData.birthDate" v-on:keyup.enter="makeRegistration()" id="datePicker">
                <select>
                    <option data-placeholder="true"></option>
                    <option value="Cote d'Ivoire">Cote d'Ivoire</option>
                    <option value="Croatia">Croatia</option>
                    <option value="Portugal">Portugal</option>
                    <option value="Cuba">Cuba</option>
                    <option value="Zimbabwe">Zimbabwe</option>
                </select>
                <input class="register__input" type="text" :placeholder="register.cityLabel" v-model="registerData.city" v-on:keyup.enter="makeRegistration()">
                <input class="register__input" type="text" :placeholder="register.addressLabel" v-model="registerData.address" v-on:keyup.enter="makeRegistration()">
                <input class="register__input" type="text" :placeholder="register.phoneLabel" v-model="registerData.phone" v-on:keyup.enter="makeRegistration()">
                <p class="errorMessage" v-if="showError">{{ register.requiredFieldMessage }}</p>
                <button class="register__button" v-on:click="makeRegistration()">{{ register.submitButtonText }}</button>
            </div>

            <div class="col-sm-1 col-lg-1"></div>
        </div>
        <div class="successMessage" v-if="showErrorMessage">
            <h1 class="register__title">Erro no registo</h1>
            <p class="errorRegister">{{ errorMessage }}</p>
        </div>
    </div>
</template>

<script>

    import axios from 'axios';
    import moment from 'moment';
    import Pikaday from 'pikaday';
    import { mapGetters } from 'vuex';
    import SlimSelect from 'slim-select';
    import { CONSTANTS } from '../main';
    import { objectToString } from '../helpers/Helpers';
    import UpdateLanguage from '../mixins/UpdateLanguage';
    import GlobalMethods from '../mixins/GlobalMethods';

    export default {

        mixins: [UpdateLanguage, GlobalMethods],

        name: 'register',

        computed: mapGetters(['getActiveLang']),

        created() {
        },

        data() {
            return {
                register: undefined,
                registerData: {
                    email: undefined,
                    password: undefined,
                    confirmPassword: undefined,
                    firstname: undefined,
                    lastname: undefined,
                    birthDate: undefined,
                    country: undefined,
                    address: undefined,
                    city: undefined,
                    phone: undefined,
                },
                showSuccessMessage: false,
                errorMessage: undefined,
                showErrorMessage: false,
                showError: false,
                slimSelect: undefined,
                spicker: undefined,
            };
        },

        mounted() {
            this.getRegisterText();
        },

        methods: {

            getRegisterText() {
                axios.get(CONSTANTS.apiUrl.concat('/GetRegisterPage?siteId=', CONSTANTS.siteId, '&language=', this.getActiveLang))
                    .then((response) => {
                        this.register = response.data;
                        setTimeout(() => {
                            if (this.slimSelect !== undefined) {
                                this.slimSelect.destroy();
                            }
                            this.convertInputs();
                        }, 250);
                    });
            },

            convertInputs() {
                // Tranformar DatePickers
                const datepickerEl = document.getElementById('datePicker');
                this.spicker = new Pikaday({
                    field: datepickerEl,
                    format: 'DD/MM/YYYY',
                    i18n: JSON.parse(this.register.datePickerTranslationJSONConfig),
                    onSelect: (d) => {
                        this.registerData.birthDate = this.spicker.toString();
                    },
                });
                // Tranformar Selects
                this.slimSelect = new SlimSelect({
                    select: 'select',
                    placeholder: this.register.countryLabel.concat(' *'),
                    onChange: (info) => {
                        this.registerData.country = info.value;
                    },
                });
            },

            makeRegistration() {
                if (this.isDefined(this.registerData.email) &&
                    this.isDefined(this.registerData.password) &&
                    this.isDefined(this.registerData.confirmPassword) &&
                    this.isDefined(this.registerData.firstname) &&
                    this.isDefined(this.registerData.country) &&
                    this.isDefined(this.registerData.lastname) &&
                    this.isDefined(this.registerData.birthDate)) {
                    this.showError = false;
                    this.showErrorMessage = false;
                    const params = objectToString({
                        Email: this.registerData.email,
                        Password: this.registerData.password,
                        ConfirmPassword: this.registerData.confirmPassword,
                        FirstName: this.registerData.firstname,
                        LastName: this.registerData.lastname,
                        BirthDate: this.isDefined(this.registerData.birthDate) ? moment(this.registerData.birthDate, 'DD-MM-YYYY').format('YYYY-MM-DD') : '',
                        Country: this.registerData.country,
                        Address: this.registerData.address,
                        City: this.registerData.city,
                        Phone: this.registerData.phone,
                    });

                    axios.post(CONSTANTS.apiAccountUrl.concat('Register'), params)
                    .then((response) => {
                        this.showSuccessMessage = true;
                    })
                    .catch((error) => {
                        this.showErrorMessage = false;
                        this.errorMessage = 'error';
                        console.log(error);
                    });
                } else {
                    this.showError = true;
                }
            },

            goToLogin() {
                this.$router.push({
                    path: '/login',
                });
            },

            onLanguageUpdated() {
                this.getRegisterText();
            },
        },
    };

</script>



